import good1 from '../../assets/img/good-1.png'
import good2 from '../../assets/img/good-2.png'
import good3 from '../../assets/img/good-3.png'
import commentImg from '../../assets/img/comment.png'

import imgsBox from '../ui/imgs.box.vue'
import fVideo from '../ui/video.vue'
export default {
	data() {
		return {
			goodImg3: good3,
			commentImg,
			showInputBox: false,
			placeholder: '',
			content: '',
			saytoCustomerId: '',
		}
	},
	props: {
		msg: {
			type: Object,
			default () {
				return {
					text: '又一批香梨成熟啦！大家赶紧来选购吧，多多优惠等着你哦',
					picPaths: [],
					adress: '运城-大丙农场',
					time: '',
					commentList: [],
				}
			}
		}
	},
	components: {
		imgsBox,
		fVideo,
	},
	computed: {
		goodMen() {
			var oldList = this.msg.praiseList;
			var list = [];
			if (oldList.length) {
				if (typeof oldList[0] === 'string') {
					list = oldList;
				} else {
					for (let i = 0; i < oldList.length; i++) {
						for (let a in oldList[i]) {
							if (/\w*name\w*/.test(a)) {
								list.push(oldList[i][a]);
								break;
							}
						}
					}
				}
			}
			return list;
		},
		commentList() {
			if (this.msg.commentList) {
				return this.msg.commentList;
			} else {
				return [];
			}
		},
		createtime() {
			return this.msg.createtimeStr.split(' ')[1];
		},
		dynamicId() {
			return this.msg.id;
		},
		imgList() {
			return this.msg.picPaths;
		},
		goodImg() {
			if (this.msg.hasPraised) {
				return good2;
			} else {
				return good1;
			}
		},
	},
	created() {},
	methods: {
		isShowInput(b, id, name) {
			this.placeholder = name ? '@' + name + ' : ' : '';
			if (id) {
				this.saytoCustomerId = id;
			} else {
				this.saytoCustomerId = '';
			}
			this.showInputBox = b;
			if (this.showInputBox) {
				this.autoFocus();
			}
		},
		autoFocus() {
			this.$nextTick(() => {
				$('.release').find('input').focus();
			});
		},
		sendComment() {
			var content = this.content;
			if (!content || /^\s*$/.test(content)) {
				window.util.toast({
					text: '评论不能为空',
					type: 'text'
				});
				return;
			}
			var url = 'addComment';
			var param = {
				dynamicId: this.dynamicId,
				content: content,
				saytoCustomerId: this.saytoCustomerId
			};
			window.util.requestPost(url, param, (res) => {
				if (res.code === 200) {
					this.isShowInput(false);
					this.content = '';
					this.updata();
				}
			});
		},
		deleteComment(id) {
			var url = 'deleteComment';
			var param = {
				commentId: id
			};
			window.util.requestPost(url, param, (res) => {
				if (res.code) {
					this.updata();
				}
			});
		},
		praiseClick() {
			var url = this.msg.hasPraised ? 'unlike' : 'like';
			var param = {
				dynamicId: this.dynamicId
			};
			window.util.requestPost(url, param, (res) => {
				if (res.code === 200) {
					this.updata();
				}
			});
		},
		updata() {
			var url = 'dynamicInfo';
			var param = {
				dynamicId: this.dynamicId
			};
			window.util.requestPost(url, param, (res) => {
				if (res.code === 200) {
					this.msg = res.data;
				}
			});
		}
	},
}




