import labelNew from '../../assets/img/label_new.png'
import labelHot from '../../assets/img/label_hot.png'
import labelSecond from '../../assets/img/label_second.png'
import labelTuan from '../../assets/img/label_tuan.png'
import allSaleImg from '../../assets/img/no_pro.png'

import imgBlock from '../ui/imgblock.vue'
import imgTest from '../../assets/img/logo.png'
export default {
	data() {
		return { allSaleImg }
	},
	props: {
		proMsg: {
			type: Object,
			default () {
				return {
					smallPic: imgTest,
					title: '这个是从一个很远的农场上运来的',
					saleCount: 15323,
					price: 45.00,
					orgPrice: 400.00,
					id: 'dssfsf',
					label: '',
					saleType: 0,
					stock: 100,
				}
			}
		},
		showType: {
			default: -1
		},
		showBorder: {
			default: true
		}
	},
	computed: {
		price() {
			if (this.proMsg.price) {
				return '￥' + parseFloat(this.proMsg.price).toFixed(2);
			} else {
				return '￥--';
			}
		},
		oldPrice() {
			if (this.proMsg.orgPrice) {
				return '￥' + parseFloat(this.proMsg.orgPrice).toFixed(0);
			} else {
				return '￥--';
			}
		},
		label() {
			var type = parseInt(this.proMsg.saleType);
			var label = '';
			switch (type) {
				case 1:
					label = labelNew;
					break;
				case 2:
					label = labelHot;
					break;
				case 3:
					label = labelSecond;
					break;
				case 4:
					label = labelTuan;
					break;
			}
			return '' //暂时先飞屏蔽标签
			// return label;
		},
		autoStyle() {
			var obj = {};
			if (!this.showBorder) {
				obj.border = 'none';
			}
			return obj;
		}
	},
	created() {
		console.log(this.allSaleImg);
	},
	components: {
		'f-img': imgBlock
	},
	methods: {
		linkToDetail() {
			var url = '/pro/' + this.proMsg.id + '/detail';
			window.util.jump(url)
		}
	}
}




