import imgBlock from '../ui/imgblock.vue'
import progressBar from '../ui/progress.bar.vue'

import img from '../../assets/img/logo.png'
export default {
	data() {
		return {}
	},
	props: {
		cardMsg: {
			type: Object,
			default () {
				return {
					img: img,
					proName: '审是右洞口拉上夺',
					pri: 45,
				}
			}
		}
	},
	components: {
		imgBlock,
		progressBar,
	}
}




