
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'

import imgBlock from '../ui/imgblock.vue'
export default {
data() {
return {
timeout: {},
oldNumber: 1,
}
},
props: {
msg: {
type: Object,
}
},
components: {
imgBlock,
InlineXNumber,
},
computed: {
number() {
return this.msg.number;
},
},
watch: {
number: function(val, oldVal) {
clearTimeout(this.timeout);
this.timeout = setTimeout(() => {
this.updateNumber(val);
}, 1000);
}
},
created() {
this.oldNumber = this.msg.number;
},
methods: {
removeItem() {
var url = 'removeGoods';
var param = {
goodsIds: this.msg.goodsId
};
window.util.confirm('移除这个商品', '', () => {
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
this.$emit('afterRemove', this.msg);
}
});
});
},
updateNumber(n) {
var url = 'updateNumber';
var param = {
goodsId: this.msg.goodsId,
number: n
};
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
console.log('添加成功');
} else {
this.msg.number = this.oldNumber;
}
});
},
},
}




