
import nothingImg from '../assets/img/empty_car.png'

import CheckIcon from 'vux/src/components/check-icon/index.vue'

import commonCard from '../components/ui/common.card.vue'
import cardEdit from '../components/control/card.edit.vue'
import nothing from '../components/ui/nothing.vue'
export default {
data() {
return {
edit: false,
proList: [],
totalMoney: 0,
allSelected: false,
nothingImg,
nothingMsg: {},
}
},
components: {
commonCard,
cardEdit,
CheckIcon,
nothing,
},
computed: {
topRightText() {
var text = this.edit ? '完成' : '编辑';
return text;
},
showNothing() {
return !this.proList.length;
},
},
created() {
this.getCarGoods();
this.setNothingMsg();
},
methods: {
changeState(b) {
if (b === undefined) {
this.edit = !this.edit;
} else {
this.edit = b;
}
},
getCarGoods() {
var url = "carGoodsList";
window.util.requestPost(url, {}, (res) => {
if (res.code == 200) {
var da = res.data;
for (let i = 0; i < da.length; i++) {
da[i].selected = false;
}
this.proList = da;

}
});
},
afterRemove(one) {
var list = this.proList;
for (let i = 0; i < list.length; i++) {
if (list[i].goodsId == one.goodsId) {
list.splice(i, 1);
console.log(i);
break;
}
}
},
toTrade() {
var list = this.proList;
var tradeList = [];
for (let i = 0; i < list.length; i++) {
if (list[i].selected) {
tradeList.push(list[i]);
}
}
var tradeObj = {
tradeList,
saleType: 1,
comeby: 'car' 
}
if (tradeList.length) {
window.util.setStor('tradeObj', tradeObj);
window.util.jump('/trade/msg');
} else {
window.util.toast({
text: '请选择你要购买的商品',
type: 'warn',
});
}
},
setNothingMsg() {
this.nothingMsg = {
img: this.nothingImg,
textList: [{ text: '亲，购物车是空的' }],
buttons: [{
text: '去逛逛',
fun: () => {
window.util.jump('/home');
}
}],
};
},
},
watch: {
allSelected: function(val) {
var list = this.proList;
var hasSlect = 0;
var allSales = 0;
for (let i = 0; i < list.length; i++) {
if (list[i].selected) {
hasSlect++;
}
if (!list[i].stock) {
allSales++;
}
}
if (val || hasSlect + allSales == list.length) {
for (let i = 0; i < list.length; i++) {
if (val && !list[i].stock) {
list[i].selected = false;
} else {
list[i].selected = val;
}
}
}
},
proList: {
handler: function(val) {
var list = this.proList;
var sum = 0;
var allSelected = true;
for (let i = 0; i < list.length; i++) {
if (!list[i].stock) {
list[i].selected = false;
}
if (!list[i].selected && list[i].stock) {
allSelected = false;
} else if (list[i].selected) {
sum += parseFloat(list[i].price || 0) * parseFloat(list[i].number);
}
}
if (list.length) {
this.allSelected = allSelected;
}
this.totalMoney = sum;
},
deep: true
}
},
}




