import ScrollList from '../components/ui/scroll.list.vue'
import CommonCard from '../components/ui/common.card.vue'

export default {
	data() {
		return {
			status: 0,
			tradeId: '',
			typeList: [],
			allTradeList: [],
			showTradeList: [],
			pageSize: 10,
			page: [0, 0, 0, 0, 0, 0],
			allLoadList: [false, false, false, false, false, false],
			loading: false,
			allLoad: false,
		}
	},
	components: {
		ScrollList,
		CommonCard,
	},
	created() {
		this.status = this.$route.params.status;
		this.tradeId = this.$route.params.id || '';
		if (this.tradeId) {
			this.typeList = [{
					text: '全部',
					status: 0
				}, {
					text: '待发货',
					status: 2
				}, {
					text: '待收货',
					status: 3
				},
				/* {
							text: '待评价',
							status: 4
						},*/
				{
					text: '已完成',
					status: 5
				}
			];
		} else {
			this.typeList = [{
					text: '全部',
					status: 0
				}, {
					text: '待付款',
					status: 1
				}, {
					text: '待发货',
					status: 2
				}, {
					text: '待收货',
					status: 3
				},
				/* {
							text: '待评价',
							status: 4
						},*/
				{
					text: '已完成',
					status: 5
				}
			];
		}
		this.getList();
	},
	methods: {
		itemStyle(one) {
			var len = this.typeList.length;
			var obj = {
				flex: len > 3 ? one.text.length : 1
			};
			return obj;
		},
		loadMore() {
			this.getList();
		},
		//修改显示列表
		selectType(one) {
			this.status = one.status;
			var hash = location.hash.split('/');
			if (this.tradeId) {
				hash[hash.length - 2] = this.status;
			} else {
				hash[hash.length - 1] = this.status;
			}
			hash = hash.join('/');
			window.util.reHref(hash);
			this.allLoad = this.allLoadList[this.status];
			if (this.allTradeList[this.status]) {
				this.showTradeList = this.allTradeList[this.status];
			} else {
				this.getList();
			}
			window.scrollTo(0, 0);
		},
		//获取列表数据
		getList() {
			var status = parseInt(this.status);
			var url = 'tradeList';
			var param = {
				pageSize: this.pageSize,
				page: ++this.page[status]
			}
			if (status) {
				param.status = status == 5 ? 6 : status;
			}

			//根据是否有tradeId取列表数据
			if (this.tradeId) {
				url = 'bigOrderList';
				param.tradeId = this.tradeId;
			}

			if (this.loading) {
				return;
			}
			this.loading = true;
			window.util.requestPost(url, param, (res) => {
				this.loading = false;
				if (res.code == 200) {
					var da = res.data;
					da.forEach((item, index) => {
						item.number = item.goodsNumber;
						item.smallpicPath = item.smallPic;
						item.title = item.goodsTitle;
						item.stock = true;
					});
					if (this.allTradeList[status]) {
						this.allTradeList[status] = this.allTradeList[status].concat(da);
					} else {
						this.$set(this.allTradeList, status, da);
					}
					this.showTradeList = this.allTradeList[this.status];
					if (da.length < this.pageSize) {
						this.allLoadList[status] = true;
						this.allLoad = true;
					}
				}
			});
		},
		deleteTradeButton(one) {
			var _this = this;
			var obj = {
				title: '提示',
				content: '删除订单',
				onConfirm: () => { _this.deleteTrade(one) }
			};
			window.util.confirm(obj);
		},
		//删除订单
		deleteTrade(one) {
			var url = 'deleteOrder';
			var param = {
				orderId: one.orderId
			}
			window.util.requestPost(url, param, (res) => {
				if (res.code == 200) {
					this.clearTradeList(one.orderId);
					this.$vux.toast.show({
						type: 'success',
						text: '删除成功'
					})
				}
			});
		},
		toTradeDetail(one) {
			var url = 'tradedetail/normal/' + one.orderId;
			window.util.jump(url);
		},
		clearTradeList(id) {
			this.allTradeList[this.status].forEach((item, index) => {
				if (item.orderId == id) {
					this.allTradeList[this.status].splice(index, 1);
				}
			});
		},
		//跳转到查看物流页面
		linkToLogistics(one) {
			var url = '/logistics/' + one.orderId;
			window.util.jump(url);
		},
		receiveConfirm(one) {
			var url = 'receiveConfirm';
			var param = {
				orderId: one.orderId
			};
			window.util.requestPost(url, param, (res) => {
				if (res.code == 200) {
					this.clearTradeList(one.orderId);
				}
			});
		},
	},
}




