import dynamicCard from '../components/control/dynamic.card.vue'
import scroll from '../components/ui/scroll.list.vue'
export default {
	data() {
		return {
			belongId: '',
			type: '',
			dynamicList: [],
			loading: false,
			allLoad: false,
			page: 1,
			pageSize: 10,
			pageFrom: 'page',
		}
	},
	components: {
		dynamicCard,
		scroll,
	},
	created() {
		this.type = this.$route.params.type;
		this.belongId = this.$route.params.belongId;
		this.pageFrom = this.$route.params.pageFrom; //page or share
		this.getData();
		this.setShare();
	},
	activated() {
		if (this.belongId != this.$route.params.belongId) {
			this.belongId = this.$route.params.belongId;
			this.dynamicList = [];
			this.page = 1;
			this.getData();
			this.setShare();
		}
	},
	methods: {
		getData() {
			if (this.loading) {
				return;
			}

			var param = {
				page: this.page,
				pageSize: this.pageSize
			};

			if (this.type == 'farm') {
				var url = 'farmDynamicList';
				param.farmId = this.belongId;
			} else {
				var url = 'dynamicList';
				param.goodsId = this.belongId;
			}
			this.loading = true;
			window.util.requestPost(url, param, (res) => {
				this.loading = false;
				if (res.code === 200) {
					if (res.data.length < this.pageSize) {
						this.allLoad = true;
					}
					var data = this.setList(res.data);
					var dyLength = this.dynamicList.length;
					if (dyLength && data[0].time === this.dynamicList[dyLength - 1].time) {
						this.dynamicList[dyLength - 1].list = this.dynamicList[dyLength - 1].list.concat(data[0].list);
						data.splice(0, 1);
					}
					this.dynamicList = this.dynamicList.concat(data);
				}
			});
		},
		setList(list) {
			var newList = [];
			var smallList = [];
			var i = 0;
			while (list.length) {
				if (!smallList.length) {
					smallList.push(list[0]);
					list.splice(0, 1);
				} else {
					if (smallList[0].createtimeStr.split(' ')[0] === list[i].createtimeStr.split(' ')[0]) {
						smallList.push(list[i]);
						list.splice(i, 1);
					} else {
						i++;
					}
				}
				if (i === list.length) {
					var obj = {
						time: this.setDate(smallList[0].createtimeStr.split(' ')[0]),
						list: smallList
					};
					newList.push(obj);
					i = 0;
					smallList = [];
				}
			};
			return newList;
		},
		setDate(time) {
			var d = new Date().Format('yyyy-MM-dd');
			if (d == time) {
				return '今天';
			} else {
				return time;
			}
		},
		loadMore() {
			this.page++;
			this.getData();
		},
		setShare() {
			var shareBaseImg = location.href.split('#')[0].split('?')[0].split('/');
			shareBaseImg.pop();
			shareBaseImg = shareBaseImg.join('/');
			shareBaseImg += '/static/img/logo.png';

			var shareObj = {
				title: window.config.share.dynamic.appMessageTitle,
				link: location.href.replace('page', 'share'),
				imgUrl: shareBaseImg,
				desc: window.config.share.dynamic.desc
			}

			window.mywx.share(shareObj, 1);

			var shareObjTimeLine = JSON.parse(JSON.stringify(shareObj));
			shareObjTimeLine.title = window.config.share.dynamic.timelineTitle;

			window.mywx.share(shareObjTimeLine, 2);
		},
		toDetail() {
			if (this.pageFrom == 'share') {
				var url = 'pro/' + this.belongId + '/page';
				window.util.jump(url);
			} else {
				history.go(-1);
			}
		},
		toFarm() {
			if (this.pageFrom == 'share') {
				var url = 'category/' + this.belongId;
				window.util.jump(url);
			} else {
				history.go(-1);
			}
		}
	},
}




