
import adress from '../assets/img/adress.png'
import Icon from 'vux/src/components/icon/index.vue'
import Msg from 'vux/src/components/msg/index.vue'

import commonCard from '../components/ui/common.card.vue'
import addrCard from '../components/ui/addr.card.vue'
export default {
data() {
return {
adress,
orderId: '',
saleType: '',
baseMsg: {},
isShowMsg: false,
msg: {
title: '',
icon: '',
buttons: [],
},
}
},
computed: {
totalCount() {
var num = this.baseMsg.price;
return num;
},
addrMsgList() {
var list = []
if (this.saleType == 'big' && this.baseMsg.receivers) {
this.baseMsg.receivers.forEach((item, index) => {
item.name = item.receiverName;
item.mobile = item.receiverMobile;
item.totalAddress = item.receiverAddress;
});
list = this.baseMsg.receivers
} else {
var obj = {
name: this.baseMsg.receiverName,
mobile: this.baseMsg.receiverMobile,
totalAddress: this.baseMsg.receiverAddress
};
list.push(obj);
}
return list;
},
},
components: {
Msg,
Icon,
commonCard,
addrCard,
},
created() {
this.orderId = this.$route.params.id;
this.saleType = this.$route.params.type;
this.getMsg();
window.mywx.payPageSet();
},
methods: {
getMsg() {
if (this.saleType == 'big') {
var url = 'bigOrderDetail';
var param = {
tradeId: this.orderId
}
} else {
var url = 'orderDetail';
var param = {
orderId: this.orderId
}
}
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
console.log(res);
var da = res.data;
da.title = da.goodsTitle;
da.number = da.goodsNumber;
da.smallpicPath = da.smallPic;
da.stock = true;
this.baseMsg = da;
}
});
},
toPay() {
if (window.config.production) {
if (this.saleType == 'big') {
var obj = {
orderIds: this.baseMsg.orderIds,
money: this.totalCount
}
} else {
var obj = {
orderIds: this.orderId,
money: this.totalCount
}
}
window.mywx.pay(obj, () => {
this.getMsg();

});
} else {
this.showMsg();
}
},
showMsg() {
var _this = this;
this.msg = {
title: '支付成功',
icon: 'success',
buttons: [{
text: '确定',
type: 'primary',
onClick: () => {
_this.isShowMsg = false;
},
}],
};
this.isShowMsg = true;
},
linkToLogistics() {
var url = '/logistics/' + this.orderId;
window.util.jump(url);
},
deleteTradeButton() {
var obj = {
title: '提示',
content: '删除订单',
onConfirm: this.deleteTrade
};
window.util.confirm(obj);
},
deleteTrade() {
if (this.saleType == 'big') {
var url = 'deleteBigTrade';
var param = {
tradeId: this.orderId
}
} else {
var url = 'deleteOrder';
var param = {
orderId: this.orderId
}
}
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
this.$vux.toast.show({
type: 'success',
text: '删除成功',
onHide() {
history.go(-1);
}
});
}
});
},
receiveConfirm() {
var url = 'receiveConfirm';
var param = {
orderId: this.orderId
};
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
this.baseMsg.orderStatus = 4;
}
});
},
},
}




