export default {
	data() {
		return {
			style: {}
		}
	},
	props: {
		src: {
			type: String
		},
		height: {
			default: 0
		}
	},
	created() {
		this.$nextTick(() => {
			this.setStyle()
		});
	},
	watch: {
		src: function(val, oldVal) {
			this.setStyle()
		},
		height: function(val, oldVal) {
			this.setStyle()
		},
	},
	methods: {
		setStyle() {
			var h;
			if (!this.height) {
				var w = $(this.$el).width();
				h = w;
			} else if (/%$/.test(this.height)) {
				var w = $(this.$el).width();
				h = w * (parseFloat(this.height) / 100);
			} else if (this.height == 'auto') {
				var w = $(this.$el).width();
				var img = document.createElement('img');
				img.src = this.src;
				img.onload = () => {
					var oh = img.height;
					var ow = img.width;
					var w = $(this.$el).width();
					h = oh * w / ow;
					var style = {
						height: h + 'px',
						background: 'url(' + this.src + ') center center / cover no-repeat'
					};
					this.style = style;
				}
			} else {
				h = parseInt(this.height);
			}
			var style = {
				height: h + 'px',
				background: 'url(' + this.src + ') center center / cover no-repeat'
			};
			this.style = style;
		}
	}
}




