import proCard from '../control/pro.card.vue'

export default {
	props: {
		proList: {
			type: Array,
			default () {
				return [{
					img: '',
					dis: '这个是从一个叫做冰月农场的农场上运来的',
					sales: 15323,
					price: 45.00,
					oldPrice: 400.00,
					id: 'dfsdfa'
				}]
			}
		}
	},
	components: {
		proCard,
	},
	data() {
		return {}
	},
	computed: {
		byside() {
			if (this.proList.length % 2 > 0) {
				return true;
			} else {
				return false;
			}
		}
	},
	created() {},
}




