import Vue from 'vue'
import Router from 'vue-router'
import Skeleton from '../pages/skeleton.vue'
import Home from '../pages/home.vue'
import ComList from '../pages/comlist.vue'
import Category from '../pages/category.vue'
import Pro from '../pages/prodetail.vue'
import ProDetail from '../components/control/pro.detail.vue'
import ProAppraisal from '../components/control/appraisal.list.vue'
import Second from '../pages/second.vue'
import Platoon from '../pages/platoonlist.vue'
import Platoonsuccess from '../pages/platoon.success.vue'
import Car from '../pages/car.vue'
import Trade from '../pages/trade.vue'
import TradeMsg from '../components/control/trade.msg.vue'
import TradeAddrList from '../components/control/trade.addrList.vue'
import TradeCardList from '../components/control/trade.cardList.vue'
import TradeList from '../pages/trade.list.vue'
import TransactionList from '../pages/transaction.list.vue'
import Me from '../pages/me.vue'
import MyCoupon from '../pages/my.coupon.vue'
import ControlAddr from '../pages/address.control.vue'
import AddAddr from '../pages/address.add.vue'
import Logistics from '../pages/logistics.vue'
import TradeDetail from '../pages/trade.detail.vue'
import Dynamic from '../pages/dynamic.vue'
import JoinUs from '../pages/transfer/join.us.vue'
import MyShop from '../pages/transfer/my.shop.vue'
import MyInvitationCode from '../pages/transfer/my.invitationcode.vue'
import Coupon from '../pages/transfer/coupon.vue'

import Building from '../pages/building.vue'

Vue.use(Router)

export default new Router({
	routes: [
		/*{
				path: '/',
				redirect: "/default/home"
			}, */
		{
			path: '/skeleton',
			component: Skeleton,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			}
		}, {
			path: '/:shoppingId/home',
			component: Home,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			}
		}, {
			path: '/:shoppingId/comlist/:type',
			component: ComList,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			}
		}, {
			path: '/:shoppingId/farm/:id',
			component: ComList,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			}
		}, {
			path: '/:shoppingId/category/:id',
			component: Category,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			}
		}, {
			path: '/:shoppingId/pro/:id',
			component: Pro,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			},
			children: [{
				path: 'detail',
				component: ProDetail
			}, {
				path: 'appraisal',
				component: ProAppraisal
			}]
		}, {
			path: '/:shoppingId/second',
			component: Second,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			},
		}, {
			path: '/:shoppingId/platoon',
			component: Platoon,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			}
		}, {
			path: '/:shoppingId/platoonsuccess/:id',
			component: Platoonsuccess,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/:shoppingId/car',
			component: Car,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/:shoppingId/trade',
			component: Trade,
			meta: {
				keepAlive: false,
			},
			children: [{
				path: 'msg',
				component: TradeMsg
			}, {
				path: 'addrlist/:type',
				component: TradeAddrList
			}, {
				path: 'cardlist/:belongId',
				component: TradeCardList
			}]
		}, {
			path: '/:shoppingId/tradelist/:status',
			component: TradeList,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/:shoppingId/tradelist/:status/:id',
			component: TradeList,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/:shoppingId/transactionlist/:status',
			component: TransactionList,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/:shoppingId/tradedetail/:type/:id',
			component: TradeDetail,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/:shoppingId/me',
			component: Me,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			}
		}, {
			path: '/:shoppingId/mycoupon',
			component: MyCoupon,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			}
		}, {
			path: '/:shoppingId/controladdr',
			component: ControlAddr,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/:shoppingId/addaddress',
			component: AddAddr,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/:shoppingId/addaddress/:receiverId',
			component: AddAddr,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/:shoppingId/logistics/:orderId',
			component: Logistics,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/:shoppingId/building',
			component: Building,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			}
		}, {
			path: '/:shoppingId/dynamic/:type/:belongId/:pageFrom',
			component: Dynamic,
			meta: {
				keepAlive: true,
				scrollTop: 0,
			}
		}, {
			path: '/joinus/:type',
			component: JoinUs,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/myshop/:type',
			component: MyShop,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/myinvitationcode',
			component: MyInvitationCode,
			meta: {
				keepAlive: false,
			}
		}, {
			path: '/coupon/:batchVersion',
			component: Coupon,
			meta: {
				keepAlive: false,
			}
		}
	]
})



