
import logo from '../../assets/img/logo.png'

import Msg from 'vux/src/components/msg/index.vue'

export default {
data() {
return {
name: '',
mobile: '',
inviteCode: '',
addr: '',
productDsc: '',
canChangeCode: true,

pageType: '',
shopMsg: {},

logo,
sellersMsg: [],
showMsg: false,
sellersMsgStyle: {},
}
},
components: {
Msg
},
created() {
this.pageType = this.$route.params.type;
this.shopMsg = window.util.getStor('shopMsg') || {};
this.getInputMsg();
this.getCode();
if (this.pageType == 'sellers') {
this.setShareMsg();
}
},
watch: {
shopMsg: function(val) {
if (val.shopCode) {
this.setSellersMsg();
this.showMsg = true;
this.setSellersMsgStyle();
}
},
},
methods: {
setSellersMsgStyle() {
this.$nextTick(() => {
var ch = $('.success_msg').height();
var wh = $(window).height();
var style = {
height: ((wh > ch ? wh : ch) + 60) + 'px'
}
this.sellersMsgStyle = style;
});
},

setSellersMsg() {
var list = [
'您已成为农场优选联盟的合伙人，请关注“农场优选联盟服务中心”公众号激活您的店铺',
'一、点击菜单 “商城 --> 我的商城” ，即可打开属于您的店铺；',
'二、点击菜单“个人中心 --> 销售商登录”，进入您的小程序关联系统；',
'三、尽快分享您的店铺，让更多人成为您的粉丝。',
'（首次通过您的店铺打开农场优选联盟系统的，即成为您的粉丝，成为粉丝后，以后在官方店铺下的单都属于您的订单，即可享有订单分成；您的粉丝如果申请成为合伙人后，则默认成为您的团队里的下级销售商，您可享有他的整体业绩的奖励）'
];
if (this.pageType == 'supplier') {
list = ['申请成功，等待客服联系']
}
if (this.shopMsg.mobile) {
list = [
'您的微信号已注册为合作伙伴',
'注册手机号为：' + this.shopMsg.mobile,
'进入“农场优选联盟服务中心”公众号',
'一、点击菜单 “商城 --> 我的商城” ，即可打开属于您的店铺；',
'二、点击菜单“个人中心 --> 销售商登录”，进入您的小程序关联系统；',
'三、尽快分享您的店铺，让更多人成为您的粉丝。',
'（首次通过您的店铺打开农场优选联盟系统的，即成为您的粉丝，成为粉丝后，以后在官方店铺下的单都属于您的订单，即可享有订单分成；您的粉丝如果申请成为合伙人后，则默认成为您的团队里的下级销售商，您可享有他的整体业绩的奖励）'
];
}
this.sellersMsg = list;
},

sendMsg() {
if (this.showMsg) {
return;
}
if (/^\s*$/.test(this.name)) {
window.util.toast({ text: '姓名不为空', type: 'text' });
return;
}
if (/^\s*$/.test(this.mobile)) {
window.util.toast({ text: '电话不为空', type: 'text' });
return;
}
if (this.pageType == 'supplier' && /^\s*$/.test(this.addr)) {
window.util.toast({ text: '产地不为空', type: 'text' });
return;
}
if (this.pageType == 'supplier' && /^\s*$/.test(this.productDsc)) {
window.util.toast({ text: '产品描述不为空', type: 'text' });
return;
}

if (this.pageType == 'sellers') {
var url = 'applySeller';
var param = {
name: this.name,
mobile: this.mobile,
recommendcode: this.inviteCode
}
var sellersParam = {
url,
param,
};
window.util.setStor('sellersParam', sellersParam);
window.mywx.getUserInfo();
} else {
var url = 'applySupplier';
var param = {
name: this.name,
mobile: this.mobile,
place: this.addr,
description: this.productDsc
}
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
this.setSellersMsg();
this.showMsg = true;
this.setSellersMsgStyle();
}
});
}

},

getCode() {
var param = window.util.getParam();
if (param.invitationcode) {
this.canChangeCode = false;
this.inviteCode = param.invitationcode;
}
},
getInputMsg() {
var sellersParam = window.util.getStor('sellersParam');
window.util.removeStor('sellersParam');
if (sellersParam) {
var param = sellersParam.param;
this.name = param.name;
this.mobile = param.mobile;
this.inviteCode = param.recommendcode;
}
},

setShareMsg() {
var shareObj = {};
var shareLink = location.href;

var shareBaseImg = location.href.split('#')[0].split('?')[0].split('/');
shareBaseImg.pop();
shareBaseImg = shareBaseImg.join('/');
shareBaseImg += '/static/img/logo.png';

shareObj.title = window.config.share.joinUs.appMessageTitle;
shareObj.link = shareLink;
shareObj.imgUrl = shareBaseImg;
shareObj.desc = window.config.share.joinUs.desc;

window.mywx.share(shareObj, 1);

var shareObjTimeLine = JSON.parse(JSON.stringify(shareObj));
shareObjTimeLine.title = window.config.share.joinUs.timelineTitle;
window.mywx.share(shareObjTimeLine, 2);
},

linkToMyShop() {
window.Global.shopcode = this.shopMsg.shopCode;
window.Global.token = this.shopMsg.token || window.util.getStor('token') || '';
var hash = '#/' + window.Global.shopcode + '/home';
window.util.removeStor('shopMsg');
if (window.Global.token) {
window.util.setStor('token', window.Global.token);
window.util.jump(hash, {}, true);
} else {
window.mywx.getUserInfo(hash);
}
},
},
}




