
import allSaleImg from '../assets/img/no_pro.png'
import ballImg from '../assets/img/ball.png'

import Swiper from 'vux/src/components/swiper/swiper.vue'

import imgBlock from '../components/ui/imgblock.vue'
import scroll from '../components/ui/scroll.list.vue'

export default {
data() {
return {
productList: [],
bannerList: [],
loading: false,
allLoad: false,
farmId: '',
page: 0,
pageSize: 10,
goodsName: '',
swiperScale: 1.4,
allSaleImg,
ballImg,
bgStyle: {},
}
},
components: {
Swiper,
imgBlock,
scroll
},
watch: {
goodsName: function(val) {
if (!val) {
this.getList(1);
}
}
},
created() {
this.farmId = this.$route.params.id;
this.init();
},
activated() {
if (this.farmId != this.$route.params.id) {
this.farmId = this.$route.params.id;
this.init();
} else {
this.getFarmMsg();
}
},
methods: {
init() {
this.page = 0;
this.bannerList = [];
this.productList = [];
this.getBenner();
this.getList();
this.getFarmMsg();
this.setAspectRatio();
},

setAspectRatio() {
var w = window.innerWidth;
var h = window.innerHeight - 150;

this.aspectRatio = h / w;
},
loadMore() {
this.getList();
},
getBenner() {
var url = 'singleBanners';
var param = {
farmId: this.farmId
};
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
var da = res.data;
for (let i = 0; i < da.length; i++) {
if (/^(www)/.test(da[i].linkurl)) {
da[i].linkurl = 'http://' + da[i].linkurl;
} else if (da[i].linkurl && (!/^(www)/.test(da[i].linkurl) || /^(http)/.test(da[i].linkurl))) {
da[i].linkurl = location.href.split('#')[0] + window.Global.shopcode + '/' + da[i].linkurl.replace(/^\//, '');
}
da[i].title = '';
da[i].url = da[i].linkurl;
da[i].img = da[i].realPath;
da[i].fallbackImg = 'https://static.vux.li/demo/3.jpg';
}
this.bannerList = da;
} else {
console.log(res);
}
})
},
getList(p) {
var page = p ? p : ++this.page;
var pageSize = p ? this.page * this.pageSize : this.pageSize;

var url = 'singleList';
var param = {
page,
pageSize,
farmId: this.farmId,
goodsName: this.goodsName
};
if (this.loading) {
return;
}
this.loading = true;
window.util.requestPost(url, param, (res) => {
this.loading = false;
if (res.code == 200) {
var data = res.data;
if (data.length < pageSize) {
this.allLoad = true;
}
data.forEach((item) => {
item.stock = Number(item.stock);
item.smallPic = item.bigPic;
});
if (page == 1) {
this.productList = data;
} else {
this.productList = this.productList.concat(data);
}
} else {
console.log(res);
}
});
},

getFarmMsg() {
var url = 'farmMsg';
var param = {
farmId: this.farmId
};
window.util.requestPost(url, param, (res) => {
var da = res.data;
var bgColors = da.backGroundColor.split(',');
console.log(bgColors);
if (bgColors.length == 1) {
var bg = this.handleBgColor(bgColors[0]);
} else {
var bg = 'linear-gradient(to bottom,';
bgColors.forEach((one, index) => {
bg += this.handleBgColor(one);
if (index != bgColors.length - 1) {
bg += ',';
}
});
bg += ')';
}
console.log(bg);
this.bgStyle = {
background: bg
};
window.util.setTitle(da.name); 
var shareMsg = {
title: da.shareTitle,
link: location.href,
imgUrl: da.picPath,
desc: da.shareContent
};
window.mywx.share(shareMsg); 
});
},
handleBgColor(color) {
if (/(rgb|\#)/.test(color)) {
return color;
} else {
return '#' + color;
}
},
onEnter() {
this.getList(1);
},
linkToProduct(one) {
var url = '/pro/' + one.id + '/detail';
window.util.jump(url);
},
linkTodynamic() {
var url = '/dynamic/farm/' + this.farmId + '/page';
window.util.jump(url);
}
},
}




