const FastClick = require('fastclick')
FastClick.attach(document.body)

import Nav from './components/control/nav.vue'
export default {
	name: 'app',
	data() {
		return {
			pageMsgList: [],
		}
	},
	created() {
		if (window.config.production) {
			window.mywx.getToken();
			window.mywx.doNotNeedTokent();
		}
		this.getShoppingId();
		this.$store.commit('sethash');
		this.setPageMsgList();
		this.setTitle();
		this.setShareMsg();
		window.onhashchange = () => {
			this.$store.commit('sethash');
			this.handelStor();
			this.setTitle();
			this.setShareMsg();
		}
	},
	components: {
		'f-nav': Nav,
	},
	methods: {
		//获取 shoppingId
		getShoppingId() {
			var shoppingId = this.$route.params.shoppingId;
			window.Global.shopcode = shoppingId;
		},
		//处理缓存
		handelStor() {
			var hash = this.$store.state.hash;
			if (!(/(trade\/msg|trade\/cardlist\/(\w*\d*)*|trade\/addrlist\/(\w*\d*)*|addaddress)$/.test(hash))) {
				window.util.removeStor('tradeObj');
				window.util.removeStor('receiverList');
			}
			if (!(/(trade\/msg|trade\/cardlist\/(\w*\d*)*)$/.test(hash))) {
				window.util.removeStor('couponCardList');
			}
			if (!(/\/addaddress\//.test(hash))) {
				window.util.removeStor('addr');
			}
			if (!(/joinus/.test(hash))) {
				window.util.removeStor('shopMsg');
			}
		},
		//每个页面的信息
		setPageMsgList() {
			var msg = [{
				hashTest: /(\/car)$/,
				title: '购物车'
			}, {
				hashTest: /(\/me)$/,
				title: '我的'
			}, {
				hashTest: /(\/comlist\/seasonal)$/,
				title: '应季鲜果'
			}, {
				hashTest: /(\/comlist\/limit)$/,
				title: '限量特产'
			}, {
				hashTest: /(\/comlist\/combination)$/,
				title: '组合礼包'
			}, {
				hashTest: /(\/comlist\/LargeCustomers)$/,
				title: '大客户专享'
			}, {
				hashTest: /(\/comlist\/new)$/,
				title: '新品体验'
			}, {
				hashTest: /(\/comlist\/all)$/,
				title: '展销厅'
			}, {
				hashTest: /(\/comlist\/hot)$/,
				title: '当季热销'
			}, {
				hashTest: /(\/farm\/\w*\d*)$/,
				title: '农场'
			}, {
				hashTest: /(\/pro\/\w*\d*\/detail)$/,
				title: '商品详情'
			}, {
				hashTest: /(\/pro\/\w*\d*\/appraisal)$/,
				title: '商品评价'
			}, {
				hashTest: /(\/controladdr)$/,
				title: '地址管理'
			}, {
				hashTest: /(\/addaddress)$/,
				title: '添加地址'
			}, {
				hashTest: /(\/trade\/\w*)$/,
				title: '创建订单'
			}, {
				hashTest: /(\/tradelist\/\w*\d*(\/*)\w*\d*)$/,
				title: '我的订单'
			}, {
				hashTest: /(\/tradedetail\/\w*\d*)$/,
				title: '订单详情'
			}, {
				hashTest: /(\/dynamic\/\w*\d*)$/,
				title: '实时动态'
			}, {
				hashTest: /(\/transactionlist\/\w*\d*)$/,
				title: '我的大客户订单'
			}, {
				hashTest: /(\/joinus\/sellers)$/,
				title: '我要加盟'
			}, {
				hashTest: /(\/joinus\/supplier)$/,
				title: '我有货源'
			}, {
				hashTest: /(\/myshop\/(get|undefined))$/,
				title: '我的店铺'
			}, {
				hashTest: /(\/myinvitationcode)$/,
				title: '我的邀请码'
			}, {
				hashTest: /(\/coupon\/(\w*\d*)*)$/,
				title: '优惠券'
			}];
			this.pageMsgList = msg;
		},
		//设置每个页面的 title
		setTitle() {
			var hash = this.$store.state.hash;
			var title = 'farm';
			var titleList = this.pageMsgList;
			titleList.forEach((item, index) => {
				if (item.hashTest.test(hash)) {
					window.util.setTitle(item.title || '');
				}
			});
		},
		//设置分享信息
		setShareMsg() {
			var shareObj = {};
			if (window.config.share.default.shareIndex) {
				var shareLink = location.href.split('#')[0] + '?#/' + window.Global.shopcode + '/home';
			} else {
				var shareLink = location.href;
			}

			var shareBaseImg = location.href.split('#')[0].split('?')[0].split('/');
			shareBaseImg.pop();
			shareBaseImg = shareBaseImg.join('/');
			shareBaseImg += '/static/img/logo.png';

			shareObj.title = window.config.share.default.appMessageTitle;
			shareObj.link = shareLink;
			shareObj.imgUrl = shareBaseImg;
			shareObj.desc = window.config.share.default.desc;

			window.mywx.share(shareObj, 1);

			var shareObjTimeLine = JSON.parse(JSON.stringify(shareObj));
			shareObjTimeLine.title = window.config.share.default.timelineTitle;
			window.mywx.share(shareObjTimeLine, 2);
		},
	},
}




