
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
data() {
return {
title: '所在地区',
name: '',
mobile: '',
addr: [],
address: '',
addressData: ChinaAddressV4Data,
isdefault: false,
receiverId: '',
}
},
components: {
Group,
XAddress,
CheckIcon,
},
created() {
this.receiverId = this.$route.params.receiverId;
if (this.receiverId) {
this.setAddr();
}
},
methods: {
getName(value) {
return value2name(value, ChinaAddressV4Data)
},
setAddr() {
var addr = window.util.getStor('addr');
this.name = addr.name;
this.mobile = addr.mobile;
this.address = addr.address;
this.isdefault = addr.isdefault;
this.addr = [addr.provice, addr.city, addr.area];
},
addAddress() {
console.log('d');
if (/^\s*$/.test(this.name)) {
this.$vux.toast.show({
text: '收货人不能为空',
type: 'text'
})
return;
}
if (/^\s*$/.test(this.mobile)) {
this.$vux.toast.show({
text: '联系电话不能为空',
type: 'text'
})
return;
}
if (/^\s*$/.test(this.address)) {
this.$vux.toast.show({
text: '收货详细地址不能为空',
type: 'text'
})
return;
}
if (this.addr.length == 0) {
this.$vux.toast.show({
text: '请选择所在地区',
type: 'text'
})
return;
}
var url = 'addAddress';
var param = {
name: this.name,
mobile: this.mobile,
provice: this.getName([this.addr[0]]),
city: this.getName([this.addr[1]]),
area: this.getName([this.addr[2]]),
address: this.address,
isdefault: this.isdefault ? 1 : 2,
};
if (this.receiverId) {
url = 'updateReceiver';
param.receiverId = this.receiverId;
}
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
if (window.util.getStor('receiverList')) {
res.data.isdefault = false;
var receiverList = window.util.getStor('receiverList');
receiverList.push(res.data);
window.util.setStor('receiverList', receiverList);
}
history.go(-1);
}
});
},
},
}




