
import Msg from 'vux/src/components/msg/index.vue'

export default {
data() {
return {
invitationCode: {},
showNoInvitationCode: false,
msg: {
title: '您没有邀请码',
icon: 'info',
description: '要成为经营者才有邀请码'
}
}
},
components: {
Msg,
},
created() {
this.invitationCode = window.util.getStor('invitationCode');
window.util.removeStor('invitationCode');
if (this.invitationCode === 'null') {
this.showNoInvitationCode = true;
} else if (!this.invitationCode) {
this.getInvitationCode();
} else {
this.setShareMsg();
}
},
methods: {
getInvitationCode() {
window.mywx.getUserInfo();
},
linkToJoin() {
var hash = 'joinus/sellers';
var url = location.href.split('#')[0] + '#/' + hash;
window.util.jump(url, {}, true);
},

setShareMsg() {
var shareObj = {};
var shareLink = location.href.split('#')[0].split('?')[0] + '?invitationcode=' + this.invitationCode.inviteCode + '#/joinus/sellers';

var shareBaseImg = location.href.split('#')[0].split('?')[0].split('/');
shareBaseImg.pop();
shareBaseImg = shareBaseImg.join('/');
shareBaseImg += '/static/img/logo.png';

shareObj.title = window.config.share.joinUs.appMessageTitle;
shareObj.link = shareLink;
shareObj.imgUrl = shareBaseImg;
shareObj.desc = window.config.share.joinUs.desc;

window.mywx.share(shareObj, 1);

var shareObjTimeLine = JSON.parse(JSON.stringify(shareObj));
shareObjTimeLine.title = window.config.share.joinUs.timelineTitle;
window.mywx.share(shareObjTimeLine, 2);
},
beforeDestroy() {
window.util.removeStor('invitationCode');
},
},

}




