import scroll from '../components/ui/scroll.list.vue'
import couponCard from '../components/ui/coupon.card.vue'
export default {
	data() {
		return {
			page: 0,
			pageSize: 10,
			couponList: [],
			loading: false,
			allLoad: false,
		}
	},
	components: {
		scroll,
		couponCard
	},
	created() {
		this.getList();
	},
	activated() {
		this.getList(1);
	},
	methods: {
		getList(p) {
			if (this.loading) {
				return
			}
			this.loading = true;
			var page = p ? p : ++this.page;
			var pageSize = p ? this.pageSize * this.page : this.pageSize;

			var url = 'couponAll';
			var param = {
				page,
				pageSize
			};

			window.util.requestPost(url, param, (res) => {
				this.loading = false;
				if (res.code == 200) {
					if (page == 1) {
						this.couponList = res.data;
					} else {
						this.couponList = this.couponList.concat(res.data);
					}
					if (res.data.length < pageSize) {
						this.allLoad = true;
					}
				}
			});
		},
		loadMore() {
			this.getList();
		}
	},
}




