import ScrollList from '../components/ui/scroll.list.vue'
import CommonCard from '../components/ui/common.card.vue'

export default {
	data() {
		return {
			status: 0,
			typeList: [],
			allTradeList: [],
			showTradeList: [],
			pageSize: 10,
			page: [0, 0, 0, 0, 0, 0],
			allLoadList: [false, false, false, false, false, false],
			loading: false,
			allLoad: false,
		}
	},
	components: {
		ScrollList,
		CommonCard,
	},
	created() {
		this.status = this.$route.params.status;
		this.typeList = [{
			text: '全部',
			status: 0
		}, {
			text: '待付款',
			status: 1
		}, {
			text: '已付款',
			status: 2
		}];
		this.getList();
	},
	methods: {
		itemStyle(one) {
			var len = this.typeList.length;
			var obj = {
				flex: len > 3 ? one.text.length : 1
			};
			return obj;
		},
		loadMore() {
			this.getList();
		},
		//修改显示列表
		selectType(one) {
			this.status = one.status;
			var hash = location.hash.split('/');
			hash[hash.length - 1] = this.status;
			hash = hash.join('/');
			window.util.reHref(hash);
			this.allLoad = this.allLoadList[this.status];
			if (this.allTradeList[this.status]) {
				this.showTradeList = this.allTradeList[this.status];
			} else {
				this.getList();
			}
			window.scrollTo(0, 0);
		},
		//获取列表数据
		getList() {
			var status = parseInt(this.status);
			var url = 'bigTradeList';
			var param = {
				pageSize: this.pageSize,
				page: ++this.page[status]
			}
			if (status) {
				param.status = status;
				if (status == 2) {
					param.status = 0;
				}
			}
			if (this.loading) {
				return;
			}
			this.loading = true;
			window.util.requestPost(url, param, (res) => {
				this.loading = false;
				if (res.code == 200) {
					var da = res.data;
					da.forEach((item, index) => {
						item.number = item.goodsNumber;
						item.smallpicPath = item.smallPic;
						item.title = item.goodsTitle;
					});
					if (this.allTradeList[status]) {
						this.allTradeList[status] = this.allTradeList[status].concat(da);
					} else {
						this.$set(this.allTradeList, status, da);
					}
					this.showTradeList = this.allTradeList[this.status];
					if (da.length < this.pageSize) {
						this.allLoadList[status] = true;
						this.allLoad = true;
					}
				}
			});
		},
		deleteTradeButton(one) {
			var _this = this;
			var obj = {
				title: '提示',
				content: '删除订单',
				onConfirm: () => { _this.deleteTrade(one) }
			};
			window.util.confirm(obj);
		},
		//删除订单
		deleteTrade(one) {
			var url = 'deleteBigTrade';
			var param = {
				tradeId: one.tradeId
			}
			window.util.requestPost(url, param, (res) => {
				if (res.code == 200) {
					var _this = this;
					this.$vux.toast.show({
						type: 'success',
						text: '删除成功',
						onHide() {
							_this.clearTradeList(one.tradeId);
						}
					})
				}
			});
		},
		//到支付详情页面
		toTradeDetail(one) {
			var url = '';
			if (one.orderStatus == 1) {
				url = 'tradedetail/big/' + one.tradeId;
			} else {
				url = '/tradelist/0/' + one.tradeId;
			}
			window.util.jump(url);
		},
		clearTradeList(id) {
			this.allTradeList[this.status].forEach((item, index) => {
				if (item.tradeId == id) {
					this.allTradeList[this.status].splice(index, 1);
				}
			});
		},
	},
}




