
import adress from '../../assets/img/adress.png'
import Icon from 'vux/src/components/icon/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import Picker from 'vux/src/components/picker/index.vue'

import commonCard from '../ui/common.card.vue'
import addrCard from '../ui/addr.card.vue'
export default {
data() {
return {
adress,
saleType: 0, //销售类型
tradeList: [],
receiverList: [], //总的收货地址列表
tradeReceivers: [], //本次交易的收货地址列表
fromShoppingCart: 0,
couponCardList: [], //优惠券列表
msg: {
title: '',
icon: '',
buttons: [],
},
isShowMsg: false,
addrCardType: 'nothing',
remark: '',
pickerNum: 0,
picValue: [],
isShowPic: false,
}
},
components: {
Msg,
Icon,
commonCard,
addrCard,
InlineXNumber,
Picker,
},
computed: {
count() {
var sum = 0;
var list = this.tradeList;
for (let i = 0; i < list.length; i++) {
sum = (parseFloat(sum) + parseFloat(list[i].number) * parseFloat(list[i].price) + parseFloat(list[i].freightPrice) * parseFloat(list[i].number) - parseFloat(list[i].couponCard.money || 0)).toFixed(2);
}
return sum;
},
sumNumber() {
var sum = 0;
var list = this.tradeList;
for (let i = 0; i < list.length; i++) {
sum += parseInt(list[i].number);
}
return sum;
},
pickerList() {
var n = this.pickerNum;
console.log(this.tradeList[n]);
var list = this.tradeList[n].communityList || [{ name: '1', value: 1 }];
return [list];
}
},
created() {
this.getCarGoods();
this.getReceiverList();
this.getCouponCardList();
window.mywx.payPageSet();
},
activated() {
this.getReceiverList();
this.getCouponCardList();
},
methods: {

getCarGoods() {
var tradeObj = window.util.getStor('tradeObj');
var tradeList = tradeObj && tradeObj.tradeList || [];
if (tradeObj && tradeObj.saleType) {
this.saleType = tradeObj.saleType;
}
if (tradeObj && tradeObj.comeby == 'car') {
this.fromShoppingCart = 1;
} else if (tradeObj && tradeObj.comeby == 'detail') {
this.fromShoppingCart = 2;
}
tradeList.forEach((item, key) => {
item.remark = '';
if (item.freightPrice) {
var freightPrice = parseFloat(item.freightPrice).toFixed(2);
item.freightPrice = freightPrice;
} else {
item.freightPrice = 0.00;
}
item.couponCard = {};
this.$set(this.tradeList, key, item);
});
this.getCommunityList();
},

getCommunityList() {
var list = this.tradeList;
for (let i = 0; i < list.length; i++) {
console.log(list[i].saleType);
if (list[i].saleType == 6) {
var url = 'communityList';
var param = {
goodsId: list[i].goodsId
};
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
res.data.forEach((item) => {
item.value = item.id;
});
list[i].communityList = res.data;
console.log(list);
}
});
}
}
},

isShowSelectCommunity(key) {
if (key || key === 0) {
this.pickerNum = key;

var n = this.pickerNum;
var list = this.tradeList[n].communityList;
this.$set(this.tradeList[n], 'community', list[0]);
}
this.isShowPic = !this.isShowPic;
},

onChange(value) {
console.log(value);
var n = this.pickerNum;
var list = this.tradeList[n].communityList;
var _this = this;
list.forEach((item) => {
if (item.value == value[0]) {
_this.$set(_this.tradeList[n], 'community', item);
}
});
},

determineNumber() {
var list = this.tradeReceivers;
var totalNum = 0;
list.forEach((item, index) => {
totalNum += item.num;
});
if (totalNum > this.tradeList[0].number) {
this.$vux.toast.show({
text: '分配数量总和过大',
type: 'warn',
});
return false;
} else if (totalNum < this.tradeList[0].number) {
this.$vux.toast.show({
text: '分配数量总和不足',
type: 'warn',
});
return false;
} else {
return true;
}
},

createdTrade() {
var list = this.tradeList;
if (!list.length) {
return;
}
if (!this.tradeReceivers.length) {
this.$vux.toast.show({
text: '收获地址不能为空',
type: 'warn',
});
return;
}
if (this.saleType == 5 && !this.determineNumber()) {
return;
}

if (this.saleType == 5) {
var url = 'createBigTtade';
var param = {
goodsId: list[0].goodsId,
remark: list[0].remark,
receiverInfos: ''
}
this.tradeReceivers.forEach((item, index) => {
if (index) {
param.receiverInfos += ',';
}
param.receiverInfos += item.id + '#' + item.num;
});
} else {
var url = 'createTtade';
var param = {
receiverId: this.tradeReceivers[0].id,
goodsInfos: '',
fromShoppingCart: this.fromShoppingCart
}
list.forEach((item, index, list) => {
if (item.saleType == 6 && (!item.community || !item.community.id)) {
window.util.toast({
text: '请选择配送小区',
type: 'text'
});
return;
}

if (index) {
param.goodsInfos += ',';
}
var remark = item.remark;
if (/^\s*$/.test(remark)) {
remark = 'null';
}
var communityId = '';
if (item.community) {
communityId = item.community.id;
}
var couponCardId = 'null';
if (item.couponCard.id) {
couponCardId = item.couponCard.id;
}
param.goodsInfos += item.goodsId + '#' + item.number + '#' + remark + '#' + couponCardId + '#' + communityId;
});
}

this.sendRequest(url, param);
},
sendRequest(url, param) {
var _this = this;
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
this.$vux.toast.show({
type: 'success',
text: '提交成功',
onHide() {
if (window.config.production) {
var obj = {
orderIds: res.data,
money: _this.count
};
window.mywx.pay(obj, (res) => {
history.go(-1);
});
} else {
_this.showMsg();
}
}
})
}
});
},
showMsg() {
this.msg = {
title: '支付成功',
icon: 'success',
buttons: [{
text: '确定',
type: 'primary',
onClick: () => {
history.go(-1);
},
}],
};
this.isShowMsg = true;
},

noAddrBlockClick() {
window.util.setStor('receiverList', this.receiverList);
var url = '/addaddress';
window.util.jump(url);
},

hasArrowClick() {
window.util.setStor('receiverList', this.receiverList);
var url = '/trade/addrlist/' + this.saleType;
window.util.jump(url);
},

linkToTradeCardList(one) {
if (this.couponCardList.length == 0) {
return;
}
window.util.setStor('couponCardList', this.couponCardList);
var url = '/trade/cardlist/' + one.goodsId
window.util.jump(url);
},

getReceiverList() {
var _this = this;
this.receiverList = window.util.getStor('receiverList') || [];
if (this.receiverList.length) {
setTradeReceivers();
} else {
var url = 'receiverList';
window.util.requestPost(url, {}, (res) => {
if (res.code == 200) {
var da = res.data;
this.receiverList = da;
setTradeReceivers(true);
}
});
}

function setTradeReceivers(isFrist) {
_this.tradeReceivers = [];
_this.addrCardType = _this.receiverList.length ? 'icon-arrow' : 'nothing';
_this.receiverList.forEach((item, index) => {
if (item.isdefault == '1' || item.isdefault === true) {
item.isdefault = true;
if (isFrist) {
item.num = Number(_this.tradeList[0].number);
}
_this.tradeReceivers.push(item);
} else {
item.isdefault = false;
item.num = 0;
}
});
if (!_this.tradeReceivers.length && _this.receiverList.length) {
_this.receiverList[0].num = Number(_this.tradeList[0].number);
_this.receiverList[0].isdefault = true;
_this.tradeReceivers.push(_this.receiverList[0]);
}
}
},

getCouponCardList() {
if (this.saleType == 5) {
return;
}
var _this = this;
this.couponCardList = window.util.getStor('couponCardList') || [];
if (!this.couponCardList.length) {
var url = 'couponEffective';
window.util.requestPost(url, {}, (res) => {
if (res.code == 200) {
var da = res.data;
da.forEach((item) => {
item.selected = false;
item.belongId = null;
});
this.couponCardList = da;
setCouponCard(true);
}
});
} else {
setCouponCard();
}

function setCouponCard(b) {
var cList = _this.couponCardList;
var tList = _this.tradeList;
if (b) {
cList.sort((a, b) => {
var value;
if (a.money > b.money) {
value = -1;
} else if (a.money < b.money) {
value = 1;
} else {
value = 0;
}
return value;
});
tList.forEach((one) => {
one.couponCard = {};
for (let i = 0; i < cList.length; i++) {
let item = cList[i];
if (!item.belongId) {
if (item.type == 3 || item.type == 2) {
item.belongId = one.goodsId;
one.couponCard = item;
break;
}
if (item.type == 1) {
var idList = item.goodsIds.split(',');
if (idList.includes(String(one.goodsId))) {
item.belongId = one.goodsId;
one.couponCard = item;
break;
}
}
}
}
});
} else {
_this.tradeList.forEach((one) => {
one.couponCard = {};
_this.couponCardList.forEach((item) => {
if (item.belongId) {
if (one.goodsId == item.belongId) {
one.couponCard = item;
}
}
});
});
}
}
}
},
}




