
import adress from '../../assets/img/adress.png'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

export default {
data() {
return {
adress,
}
},
props: {
msg: {
type: Object,
},
type: {
type: String,
default: '' //isControl(有编辑控件),noControl(没有编辑控件),nothing(空)
}
},
computed: {
checkText() {
var text = this.type == 'isControl' ? '设为默认地址' : '设置';
return text;
},
totalAddress() {
var addr = '';
var msg = this.msg;
if (this.msg.totalAddress) {
addr = this.msg.totalAddress;
} else {
if (msg.provice) {
addr += msg.provice;
}
if (msg.city) {
addr += msg.city;
}
if (msg.area) {
addr += msg.area;
}
if (msg.address) {
addr += msg.address;
}
}
return addr;

},
},
components: {
CheckIcon,
Icon
},
methods: {
check() {
this.$emit('checkFun', this.msg);
},
deleteFun() {
this.$emit('deleteAddr', this.msg);
},
eaditAddr() {
this.$emit('eaditAddr', this.msg);
},
hasArrowClick() {
this.$emit('hasArrowClick', this.msg);
},
noAddrBlockClick() {
this.$emit('noAddrBlockClick');
},
},
}




