import cardTop from '../../assets/img/card_top.png'
import cardCenter from '../../assets/img/card_center.png'
import bgImg from '../../assets/img/coupon_bg.png'
import topImg from '../../assets/img/coupon_top.png'
import envelopeImg from '../../assets/img/coupon_env_top.png'
export default {
	data() {
		return {
			topImg,
			envelopeImg,
			cardTop,
			cardCenter,
			bgStyle: {},
			showMask: false,
			batchVersion: '',
			couponMsg: {},
		}
	},
	created() {
		this.batchVersion = this.$route.params.batchVersion;
		console.log(this.batchVersion);
		this.getMsg();
		this.setBgStyle();
	},
	computed: {
		envelopeStyle: function() {
			var obj = {};
			var w = window.innerWidth;
			var h = window.innerHeight;
			if (h / w >= 1.9) {
				obj = {
					marginTop: '30%'
				}
			}
			if (h / w <= 570 / 320) {
				obj = {
					marginTop: '8%'
				}
			}
			return obj;
		}
	},
	methods: {
		setBgStyle() {
			this.bgStyle = {
				height: window.innerHeight + 'px',
				background: 'url(' + bgImg + ') top center / cover no-repeat',
			};
		},
		maskStatus(b) {
			if (b !== undefined) {
				this.showMask = b;
			} else {
				this.showMask = !this.showMask;
			}
		},
		getMsg() {
			var url = 'couponMsg';
			var param = {
				batchVersion: this.batchVersion
			};
			window.util.requestPost(url, param, (res) => {
				if (res.code == 200) {
					this.couponMsg = res.data;
				}
			});
		},
		getCard() {
			var url = 'couponReceive';
			var param = {
				batchVersion: this.batchVersion
			};
			window.util.requestPost(url, param, (res) => {
				if (res.code == 200) {
					this.couponMsg.isReceived = 1;
					for (var k in res.data) {
						this.couponMsg[k] = res.data[k];
					}
					this.maskStatus();
				}
			});
		},
		linkToHome() {
			window.Global.shopcode = this.couponMsg.shopCode;
			var url = '/home';
			window.util.jump(url, {}, true);
		},
	}
}




