
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
data() {
return {}
},
props: {
loading: {
type: Boolean,
default: false
},
allLoad: {
type: Boolean,
default: false
},
endTip: {
type: Boolean,
default: true
},
},
components: {
LoadMore,
},
computed: {
loadTip() {
var tip;
if (this.loading) {
tip = '正在加载';
} else if (this.allLoad) {
tip = '没有更多了'
}
return tip;
},
},
created() {
this.onscroll();
},
activated() {
this.onscroll();
},
methods: {
onscroll() {
window.onscroll = () => {
var scrollTop = $(window).scrollTop();
var winH = $(window).height();
var docH = $(document).height();
if (docH - winH - scrollTop < 10) {
if (!this.loading && !this.allLoad) {
this.$emit('loadMore');
}
}
}
}
},
}




