
import Icon from 'vux/src/components/icon/index.vue'

import addrCard from '../ui/addr.card.vue'
export default {
data() {
return {
addCardType: 'noControl',
receiverList: [],
saleType: 0,
}
},
components: {
addrCard,
Icon
},
created() {
this.saleType = this.$route.params.type;
console.log(this.saleType);
this.getReceiverList();
},
activated() {
this.getReceiverList();
},
methods: {
getReceiverList() {
this.receiverList = window.util.getStor('receiverList');
},
checkFun(one) {
var type = this.saleType;
if (type != 5) {
this.receiverList.forEach((item, index) => {
if (one.id != item.id) {
item.isdefault = false;
}
});
this.backPage();
}
},
backPage() {
window.util.setStor('receiverList', this.receiverList);
history.go(-1);
},
addAddr() {
var url = '/addaddress';
window.util.jump(url);
},
}
}




