import couponCard from '../ui/coupon.card.vue'
export default {
	data() {
		return {
			belongId: '',
			couponCardList: [],
		}
	},
	components: {
		scroll,
		couponCard
	},
	created() {
		this.belongId = this.$route.params.belongId;
		console.log(this.belongId);
		this.getList();
	},
	activated() {
		this.belongId = this.$route.params.belongId;
		this.getList();
	},
	methods: {
		getList() {
			var _this = this;
			this.couponCardList = window.util.getStor('couponCardList');
			this.couponCardList.forEach((item) => {
				if (item.belongId != this.belongId) {
					item.selected = false;
					if (item.belongId) {
						item.unUse = true;
					} else if (item.type == 1) {
						var idList = item.goodsIds.split(',');
						if (!idList.includes(String(_this.belongId))) {
							item.unUse = true;
						}
					}
				} else {
					item.unUse = false;
					item.selected = true;
				}
			});
		},
		checkFun(one) {
			var _this = this;
			if (!one.unUse && (one.belongId === null || one.belongId == this.belongId)) {
				this.couponCardList.forEach((item) => {
					if (one.id == item.id) {
						this.couponCardList.forEach((one) => {
							if (one.belongId == _this.belongId) {
								one.belongId = null;
							}
						});
						item.selected = !item.selected;
						if (item.selected) {
							item.belongId = _this.belongId;
						} else {
							item.belongId = null;
						}
					} else {
						item.selected = false;
					}
				});
			}
		},
		backPage() {
			window.util.setStor('couponCardList', this.couponCardList);
			history.go(-1);
		},
	},
}




