
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import addrCard from '../components/ui/addr.card.vue'
export default {
data() {
return {
receiverList: [],
allSelected: true,
oldDefaultId: null,
}
},
components: {
CheckIcon,
addrCard,
},
created() {
this.getReceiverList();
},
watch: {},
methods: {
linkToAddAddr() {
var url = '/addaddress';
window.util.jump(url);
},
checkFun(one) {
this.changeDefaultClick(one);
},
setdefault(one) {
var url = 'setdefault';
var param = {
receiverId: one.id,
}
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
this.oldDefaultId = one.id;
} else {
this.receiverList.forEach((item, index, _this) => {
item.isdefault = false;
if (item.id == this.oldDefaultId) {
item.isdefault = true;
}
});
}
});
},
eaditAddr(one) {
window.util.setStor('addr', one);
var hash = '/addaddress/' + one.id;
window.util.jump(hash);
},
deleteAddr(one) {
var _this=this;
window.util.confirm('是否要删除这个地址', '提示', () => {
doDelete(one);
});

function doDelete(one) {
var url = 'deleteAddr';
var param = {
receiverId: one.id,
}
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
_this.receiverList.forEach((item, index) => {
item.isdefault = false;
if (item.id == one.id) {
_this.receiverList.splice(index, 1);
}
});
} else {
this.$vux.toast.show({
text: '删除失败'
});
}
});
}
},
changeDefaultClick(one) {
var _this = this;
this.receiverList.forEach((item, index, _this) => {
item.isdefault = false;
});
one.isdefault = true;
this.setdefault(one);
},
getReceiverList() {
var url = 'receiverList';
window.util.requestPost(url, {}, (res) => {
if (res.code == 200) {
var da = res.data;
this.receiverList = da;
var _this = this;
da.forEach((item, index, _this) => {
if (item.isdefault == '1') {
item.isdefault = true;
this.oldDefaultId = item.id;
} else {
item.isdefault = false;
}
});

}
});
}
}
}




