export default {
	props: {
		msg: {
			type: Object,
			default () {
				return {
					img: '',
					textList: [{
						text: '',
						style: {}
					}],
					buttons: [{
						text: '',
						fun: () => {}
					}]
				}
			}
		},
	},
	computed: {
		img() {
			return this.msg.img || '';
		},
		pText() {
			return this.msg.textList || [];
		},
		buttons() {
			return this.msg.buttons || [];
		}
	},
	methods: {
		buttonClick(one) {
			if (one.fun) {
				one.fun();
			} else {}
		}
	},
}




