
import imgBlock from '../ui/imgblock.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


import footImg from '../../assets/img/salcefoot.png'
export default {
data() {
return {
footImg,
num: 1,
minNum: 1,
showSelect: false,
proId: '',
type: 0,

baseData: {},
}
},
props: {
proMsg: {
type: Object,
}
},
components: {
imgBlock,
Group,
Cell,
InlineXNumber,
Icon,
},
created() {
this.proId = this.$route.params.id;
this.getBaseDetail();
},
methods: {
sumbit() {
var url;
if (this.type == 0) {
url = 'joinGoods';
var param = {
goodsId: this.proId,
number: this.num
};
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
console.log('成功');
window.util.jump('/car');
}
});
} else if (this.type == 1) {
var da = this.baseData;
if (!da.stock) {
window.util.alert('库存为 0，不可购买');
return;
}
var tradeList = [];
var obj = {
goodsId: da.id,
title: da.title,
price: da.price,
smallpicPath: da.smallPic,
priceOrg: da.orgPrice,
number: this.num,
freightPrice: da.freightPrice,
stock: da.stock,
saleType: da.saleType
};
tradeList.push(obj);
var tradeObj = {
tradeList,
comeby: 'detail',
saleType: da.saleType
};
window.util.setStor('tradeObj', tradeObj);
window.util.jump('/trade/msg');
}
},
buyButton(type) {
this.type = type;
if (type == 1) {
console.log('购买');
} else if (type == 0) {
console.log('加入购物车');
}
this.showSelect = true;
},
linkToIndex() {
var url = '/home';
window.util.jump(url);
},
cancelBuy() {
this.showSelect = false;
},
getBaseDetail() {
var url = "proBaseDetail"
var param = {
goodsId: this.proId
};
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
this.baseData = res.data;
this.minNum = res.data.bigMinnumber || 1;
} else {
console.log('baseData get failt');
}
});
},
}
}




