import touImg from '../assets/img/people-logo.png'

import my1 from '../assets/img/my_1.png'
import my2 from '../assets/img/my_2.png'
import my3 from '../assets/img/my_3.png'
import my4 from '../assets/img/my_4.png'
import my5 from '../assets/img/my_5.png'

import imgBlock from '../components/ui/imgblock.vue'
export default {
	data() {
		return {
			touImg,
			tabList: [],
			userInfo: {},
			cellList: [],
		}
	},
	components: {
		imgBlock,
	},
	created() {
		this.tabList = [{
				icon: my1,
				text: '待付款',
				num: 0
			}, {
				icon: my2,
				text: '待发货',
				num: 0
			}, {
				icon: my3,
				text: '待收货',
				num: 0
			},
			/*{
				icon: my4,
				text: '待评价',
				num: 0
			},
			 {
						icon: my5,
						text: '退款/售后',
						num: 0
					},*/
		];
		this.cellList = [{
			text: '大客户订单',
			link: '/transactionlist/0'
		}, {
			text: '我的优惠券',
			link: '/mycoupon'
		}, {
			text: '收货地址管理',
			link: '/controladdr'
		}];
		this.getUserInfo();
	},
	activated() {
		this.getUserInfo();
	},
	methods: {
		getUserInfo() {
			var url = 'myinfo';
			window.util.requestPost(url, {}, (res) => {
				if (res.code == 200) {
					var da = res.data;
					this.userInfo = da;
					this.tabList[0].num = da.waitPayNumber;
					this.tabList[1].num = da.waitDeliverNumber;
					this.tabList[2].num = da.waitReceiveNumber;
					this.tabList[3] ? this.tabList[3].num = da.waitRateNumber : '';
				}
			});
		},
		linkToNextPage(one) {
			var url = one.link;
			window.util.jump(url);
		},
		linkToTradeList(param) {
			var url = '/tradelist/' + (param ? param : 0);
			window.util.jump(url);
		}
	},
}




