window.Global = {
	token: window.dev.token,
	shopcode: window.dev.shopcode,
};

//缓存页面滚动条位置
window.util.vueMixin({
	activated() {
		this.$nextTick(() => {
			if (this.$route.meta.scrollTop) {
				$(window).scrollTop(this.$route.meta.scrollTop);
				this.$route.meta.scrollTop = 0;
			}
		});
	},
	beforeRouteLeave(to, from, next) {
		window.onscroll = null;
		//从我的推荐码页面离开，清除缓存 invitationCode
		if (from.path == '/myinvitationcode') {
			window.util.removeStor('invitationCode');
		}
		from.meta.scrollTop = $(window).scrollTop();
		next();
	},
});

window.mywx = {
	//页面注册
	config: (checkToken) => {
		var l = location;
		var configUrl = location.href.split('#')[0];
		var url = 'getAuthInfo';
		var param = {
			url: configUrl,
			checkToken
		};
		window.util.requestPost(url, param, (res) => {
			if (res.code == 200) {
				var da = res.data;
				wx.config({
					debug: window.config.wxDebug, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
					appId: da.appId, // 必填，公众号的唯一标识
					timestamp: da.timestamp, // 必填，生成签名的时间戳
					nonceStr: da.nonceStr, // 必填，生成签名的随机串
					signature: da.signature, // 必填，签名，见附录1
					jsApiList: [
						'chooseWXPay',
						'onMenuShareTimeline',
						'onMenuShareAppMessage',
						'previewImage',
					] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
				});
			} else if (res.code == 605) {
				window.util.removeStor('token');
				// location.reload();
				window.mywx.getUserInfo();
			}
		})
	},
	//用户code
	getUserInfo: (hash) => {
		var isWx = window.util.isBrowser('wx');
		if (isWx) {
			var l = location;
			var state = encodeURIComponent(hash || l.hash);
			var redirectUri = l.origin + l.pathname;
			var url = 'https://open.weixin.qq.com/connect/oauth2/authorize?' +
				'appid=' + window.config.appid +
				'&redirect_uri=' + redirectUri +
				'&response_type=code&scope=snsapi_userinfo&state=' + state +
				'#wechat_redirect';
			location.href = url;
		}
	},
	//获取 token 以及页面中转
	getToken: () => {
		var urlParam = window.util.getParam();
		if (urlParam.state) {
			urlParam.state = decodeURIComponent(urlParam.state);
		}
		var hash = location.hash;
		var isMyShop = /myshop\/get$/.test(urlParam.state) || /myshop\/get$/.test(urlParam.hash);
		var isInvitationCode = /myinvitationcode$/.test(urlParam.state) || /myinvitationcode$/.test(hash);
		var isJoinUs = /joinus\/(sellers|supplier)$/.test(urlParam.state) || /joinus\/(sellers|supplier)$/.test(hash);
		if (isMyShop || isInvitationCode || isJoinUs) {
			return;
		}
		if (/(\w*\d*)+-(\w*\d*)+-(\w*\d*)+-(\w*\d*)+/.test(urlParam.state)) {
			var shopcode = urlParam.state.split('/')[1];
			window.Global.shopcode = shopcode;
		}
		if (urlParam.code) {
			var url = 'login';
			var param = {
				code: urlParam.code
			};
			window.util.requestPost(url, param, (res) => {
				if (res.code == 200) {
					window.Global.token = res.data.token;
					window.util.setStor('token', window.Global.token);
					var state = urlParam.state;
					window.util.jump(state, {}, true);
				} else {
					if (window.config.wxDebug) {
						window.util.alert('登录失败');
					}
				}
			});
		} else {
			var token = window.util.getStor('token');
			if (token) {
				window.Global.token = token;
				if (window.config.wxConfig) {
					window.mywx.config(1);
				}
			} else {
				window.mywx.getUserInfo();
			}
		}
	},
	//不需要 token 的页面中转
	doNotNeedTokent() {
		var urlParam = window.util.getParam();
		if (urlParam.state) {
			urlParam.state = decodeURIComponent(urlParam.state);
		}
		var hash = location.hash;
		var isMyShop = /myshop\/get$/.test(urlParam.state) || /myshop\/get$/.test(urlParam.hash);
		var isInvitationCode = /myinvitationcode$/.test(urlParam.state) || /myinvitationcode$/.test(hash);
		var isJoinUs = /joinus\/(sellers|supplier)$/.test(urlParam.state) || /joinus\/(sellers|supplier)$/.test(hash);
		if (!isMyShop && !isInvitationCode && !isJoinUs) {
			return;
		}
		if (urlParam.code) {
			var param = {
				code: urlParam.code
			};
			if (isMyShop) {
				var url = 'getShopCode';
			} else if (isInvitationCode) {
				var url = 'getInviteCode';
			} else if (isJoinUs) {
				var sellersParam = window.util.getStor('sellersParam');
				var url = sellersParam.url;
				param = sellersParam.param;
				param.code = urlParam.code;
			}
			window.util.requestPost(url, param, (res) => {
				if (res.code == 200) {
					if (isMyShop) {
						var hash = res.data.shopCode + '/home';
						var url = location.href.split('?')[0] + '?#/' + hash;
						window.util.jump(url, {}, true);
					} else if (isInvitationCode) {
						window.util.setStor('invitationCode', res.data);
						var state = urlParam.state;
						var url = location.href.split('?')[0] + state;
						window.util.jump(url, {}, true);
					} else if (isJoinUs) {
						window.util.setStor('shopMsg', res.data);
						var state = urlParam.state;
						var url = location.href.split('?')[0] + state;
						window.util.jump(url, {}, true);
					}
				} else {
					if (isMyShop) {
						var hash = 'myshop/undefined'
						var url = location.href.split('?')[0] + '#/' + hash;
						window.util.jump(url, {}, true);
					} else if (isInvitationCode) {
						if (res.code == 610) {
							window.util.removeStor('invitationCode');
						} else {
							window.util.setStor('invitationCode', 'null');
						}
						var state = urlParam.state;
						var url = location.href.split('?')[0] + state;
						window.util.jump(url, {}, true);
					} else if (isJoinUs) {
						window.util.removeStor('sellersParam');
						window.util.setStor('remindMsg', res.message);
						var state = urlParam.state;
						var url = location.href.split('?')[0] + state;
						window.util.jump(url, {}, true);
					}
				}
			});
		} else {
			if (window.config.wxConfig) {
				window.mywx.config(0);
			}
		}
	},
	//支付页面的 location 设置
	payPageSet() {
		var l = location;
		if (l.href.indexOf('?') == -1) {
			var url = l.href.split('#').join('?#');
			l.replace(url);
		}
	},
	//发起支付
	pay: (obj, success, faild) => {
		var getTimes = 0;
		var orderIds = obj.orderIds;
		var money = Number(obj.money);
		var param = {
			orderIds,
			money,
		}

		var url = 'pay';
		window.util.requestPost(url, param, (res) => {
			if (res.code == 200) {
				getPayMsg();
			} else {
				faild ? faild(res) : window.util.alert(res.message);
			}
		});

		function getPayMsg() {
			getTimes++; //控制只能回调次数
			if (getTimes > window.config.payGetTimes) {
				window.util.alert('请求超时，支付失败');
				return;
			}
			var url = 'payrecord';
			var param = {
				orderIds,
			};
			window.util.requestPost(url, param, (res) => {
				if (res.code == 200) {
					var da = res.data;
					wx.chooseWXPay({
						appId: da.appId,
						timestamp: Number(da.timeStamp), // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
						nonceStr: da.nonceStr, // 支付签名随机串，不长于 32 位
						package: da.packageStr, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
						signType: da.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
						paySign: da.paySign, // 支付签名
						success: function(res) {
							// 支付成功后的回调函数
							success && success();
						}
					});
				} else if (res.code == 660) {
					setTimeout(() => {
						getPayMsg();
					}, 2000);

				} else {
					faild ? faild() : window.util.alert('发起支付失败');
				}
			});
		}
	},
	//分享
	share(obj, type) {
		if (window.config.wxConfig) {
			wx.ready(() => {
				if (type == 1) {
					wx.onMenuShareAppMessage(obj);
				} else if (type == 2) {
					wx.onMenuShareTimeline(obj);
				} else {
					wx.onMenuShareTimeline(obj);
					wx.onMenuShareAppMessage(obj);
				}
			});
		}
	}
}



