
import head from '../assets/img/head_gray.png'

import imgBlock from '../components/ui/imgblock.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'

export default {
data() {
return {
orderId: '',
head,
expressList: [],
baseMsg: {},
}
},
components: {
imgBlock,
TimelineItem,
Timeline,
},
created() {
this.orderId = this.$route.params.orderId;
this.getMsg();
this.getBaseMsg();
},
methods: {
getMsg() {
var url = 'expressInfo';
var param = {
orderId: this.orderId,
}
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
this.expressList = res.data;
}
});
},
getBaseMsg() {
var url = 'expressbaseinfo';
var param = {
orderId: this.orderId,
}
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
this.baseMsg = res.data[0];
}
});
},
filterTime(time) {
var newTime = new Date(time).Format('yyyy-MM-dd hh:mm:ss');
return newTime;
},
},
}




