import player from '../../assets/img/play.png'

import imgBlock from '../ui/imgblock.vue'
export default {
	data() {
		return {
			video: {},
			vBoxStyle: {},
			playerImg: player,
			showMask: true,
		}
	},
	props: {
		src: {
			type: String
		},
		height: {
			type: String,
			default: ''
		},
		type: {
			type: Number,
			default: 0
		},
	},
	components: {
		imgBlock,
	},
	computed: {
		vHeight() {
			if (this.type == 1) {
				var h = document.documentElement.clientWidth * 3 / 5;
				h = this.height ? this.height : h;
				var obj = {};
				obj.width = '100%';
				obj.height = h;
				this.vBoxStyle = obj;
				return h;
			} else {
				return ''
			}
		},
	},
	created() {
		var _this = this;
		this.$nextTick(() => {
			this.video = $(this.$el).find('video')[0];
			this.monitor();
			this.video.addEventListener('loadeddata', (e) => {
				_this.setStyle();
				_this.init();
			});
		});
	},
	methods: {
		init() {
			// this.play();
			// setTimeout(this.pause, 100);
		},
		blockClick() {
			if (this.showMask) {
				this.play();
			} else {
				this.pause();
				this.exitFullscreen();
			}
		},
		play() {
			this.video.play();
			this.requestFullScreen();
		},
		pause() {
			this.video.pause();
		},
		setStyle(video) {
			var video = this.video;
			var obj = {};
			var vw = video.videoWidth;
			var vh = video.videoHeight;
			var w = 0,
				h = 0;
			if (vw > vh) {
				w = $(this.$el).width();
				h = vh * (w / vw);
			} else {
				w = 200;
				h = vh * (w / vw);
			}
			obj = {
				width: w + 'px',
				height: h + 'px',
			}
			if (this.type == 1) {
				obj.width = '100%';
				obj.height = document.documentElement.clientWidth * 3 / 5;
			}
			this.vBoxStyle = obj;
		},
		//监听
		monitor() {
			var _this = this;
			this.video.addEventListener('play', (e) => {
				_this.showMask = false;
			});
			this.video.addEventListener('pause', (e) => {
				_this.showMask = true;
			});
		},
		//进入全屏
		requestFullScreen() {
			var de = this.video;
			if (de.requestFullscreen) {
				de.requestFullscreen();
			} else if (de.mozRequestFullScreen) {
				de.mozRequestFullScreen();
			} else if (de.webkitRequestFullScreen) {
				de.webkitRequestFullScreen();
			}
		},
		//退出全屏
		exitFullscreen() {
			var de = this.video;
			if (de.exitFullscreen) {
				de.exitFullscreen();
			} else if (de.mozCancelFullScreen) {
				de.mozCancelFullScreen();
			} else if (de.webkitCancelFullScreen) {
				de.webkitCancelFullScreen();
			}
		},
		captureImage() {
			var canvas = document.createElement("canvas");
			canvas.width = $(this.$el).width();
			canvas.height = this.vBoxStyle.height;
			canvas.getContext('2d').drawImage(this.video, 0, 0, canvas.width, canvas.height);
			var img = document.createElement("img");
			img.src = canvas.toDataURL("image/png");
		}
	}
}




