
import outer from '../../assets/img/outer.png'
import used from '../../assets/img/used.png'
import used2 from '../../assets/img/used_2.png'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
data() {
return {
outerImg: outer,
outerStyle: {},
}
},
props: {
msg: {
type: Object,
default () {
return {
money: 12,
startlineStr: '2018.08.03',
deadlineStr: '2018.08.03',
rule: '限酱菜店铺用',
id: 122,
type: 1,
selected: false,
unUse: false,
}
}
}
},
components: {
CheckIcon
},
computed: {
isOut: function() {
var state = this.msg.state;
if (state > 3) {
return true;
} else {
return false;
}
},
isShowImg: function() {
var state = this.msg.state;
switch (state) {
case 3:
this.outerImg = used2;
break;
case 4:
this.outerImg = used;
break;
case 5:
this.outerImg = outer;
break;
}
if (state >= 3) {
return true;
} else {
return false;
}
},

},
created() {
this.draw();
this.setOuterStyle();
},
methods: {
draw() {
this.$nextTick(() => {
var canvasBox = $(this.$el).find('.canvas');
var w = $(canvasBox).width();
var h = $(canvasBox).height() - 10;

var canvas = document.createElement('canvas');

canvas.width = w;
canvas.height = h;
canvas.style.position = 'relative';
canvas.style.top = '5px';

$(canvasBox).prepend(canvas);
canvas = canvas.getContext("2d");

canvas.fillStyle = '#fff';
canvas.fillRect(0, 0, w, h);

canvas.translate(w / 2, 0);

var clearW = w - 3;
var num = 5;
var interval = (h - clearW * num) / (num + 1);

function
getNumInterval(i) {
if (i < 5) {
num--;
interval = (h - clearW * num) / (num + 1);
getNumInterval(interval);
} else if (i > 8) {
num++;
interval = (h - clearW * num) / (num + 1);
getNumInterval(interval);
}
}
getNumInterval(interval);

for (let i = 0; i < num; i++) {
if (i == 0) {
var cy = interval + clearW / 2;
} else {
var cy = interval + clearW;
}
canvas.translate(0, cy);
canvas.clearRect(-clearW / 2, -clearW / 2, clearW, clearW);
}
});
},
setOuterStyle() {
var right = Math.stochastic(0, 15) + 'px';
var top = Math.stochastic(0, 20) + 'px';
var transform = 'rotate(' + Math.stochastic(-30, 30) + 'deg)';
var obj = {
top,
right,
transform
};
this.outerStyle = obj;
},
},
}




