import baseList from '../components/control/baselist.vue'
import scrollList from '../components/ui/scroll.list.vue'
export default {
	data() {
		return {
			type: 1,
			farmId: '',
			proList: [],
			loading: false,
			allLoad: false,
			requestUrl: '',

			page: 0,
			pageSize: 10,
		}
	},
	components: {
		baseList,
		scrollList,
	},
	watch: {
		// type: function(val) {
		// 	if (val == 'farm') {
		// 		if (this.farmId == this.$route.params.id) {
		// 			this.reInit();
		// 		} else {
		// 			this.farmId = this.$route.params.id;
		// 		}
		// 	} else {
		// 		this.reInit();
		// 	}
		// },
		// requestUrl: function(val) {
		// 	if (this.type == 'farm') {
		// 		if (this.farmId == this.$route.params.id) {
		// 			this.reInit();
		// 		} else {
		// 			this.farmId = this.$route.params.id;
		// 		}
		// 	} else {
		// 		this.reInit();
		// 	}
		// },
		farmId: function(val) {
			this.setTitle();
			this.reInit();
		},
	},
	created() {
		var typeObj = this.getType();
		this.type = typeObj.type;
		this.requestUrl = typeObj.requestUrl;
		this.getList();
	},
	activated() {
		var typeObj = this.getType();
		if (this.type != typeObj.type || this.requestUrl != typeObj.requestUrl) {
			this.type = typeObj.type;
			this.requestUrl = typeObj.requestUrl;
			if (this.type == 'farm') {
				if (this.farmId == this.$route.params.id) {
					this.reInit();
				} else {
					this.farmId = this.$route.params.id;
				}
			} else {
				this.reInit();
			}
		}
	},
	methods: {
		reInit() {
			this.page = 0;
			this.proList = [];
			this.allLoad = false;
			this.getList();
			$(window).scrollTop(0);
		},
		//设置列表类型
		getType() {
			var type = '',
				requestUrl = '';
			switch (this.$route.params.type) {
				case 'seasonal':
					type = 1;
					requestUrl = 'listByGoodsType';
					break;
				case 'limit':
					type = 2;
					requestUrl = 'listByGoodsType';
					break;
				case 'combination':
					type = 3;
					requestUrl = 'listByGoodsType';
					break;
				case 'new':
					type = 1;
					requestUrl = 'listByType';
					break;
				case 'hot':
					type = 2;
					requestUrl = 'listByType';
					break;
				case 'LargeCustomers':
					type = 5;
					requestUrl = 'listByType';
					break;
				case 'all':
					type = '';
					requestUrl = 'listByFarm';
					break;
				default:
					type = 'farm';
					requestUrl = 'listByFarm';
			};
			return {
				type,
				requestUrl
			};
		},
		loadMore() {
			this.getList();
		},
		//获取列表数据
		getList() {
			var param = {
				page: ++this.page,
				pageSize: this.pageSize
			}
			var url = this.requestUrl;
			if (url === 'listByType') {
				param.saleType = this.type;
			} else if (url === 'listByGoodsType') {
				param.goodsType = this.type;
			} else {
				param.farmId = this.farmId;
			}
			this.loading = true;
			window.util.requestPost(url, param, (res) => {
				if (res.code == 200) {
					var da = res.data;
					this.loading = false;
					this.proList = this.proList.concat(da);
					if (da.length < this.pageSize) {
						this.allLoad = true;
					}
				}
			})
		},
		//当type为farm 时，设置title
		setTitle() {
			var url = 'farmMsg';
			var param = {
				farmId: this.farmId
			};
			window.util.requestPost(url, param, (res) => {
				var da = res.data;
				window.util.setTitle(da.name);
			});
		},
	}
}




