import imgBlock from './imgblock.vue'
export default {
	data() {
		return {
			style: {}
		}
	},
	props: {
		imgList: {
			type: Array,
			default () {
				return []
			}
		}
	},
	components: {
		imgBlock,
	},
	computed: {
		list() {
			var smallList = [],
				list = [],
				n = 3;
			if (this.imgList.length === 4) {
				n = 2;
			}
			var maxLength = this.imgList.length <= 9 ? this.imgList.length : 9;
			for (let i = 0; i < maxLength; i++) {
				smallList.push(this.imgList[i]);
				if (smallList.length === n) {
					list.push(smallList);
					smallList = [];
				}
				if (i === maxLength - 1 && maxLength !== 3) {
					list.push(smallList);
				}
			}
			// 一个图片时的图片大小设置
			this.setStyle(list);
			return list;
		}
	},
	methods: {
		showBigImg(url) {
			wx.previewImage({
				current: url,
				urls: this.imgList
			});
		},
		setStyle(list) {
			if (list.length != 1) {
				return {};
			}
			var obj = {},
				h = 0,
				w = 0,
				img = new Image();
			img.src = list[0];
			img.onload = () => {
				h = img.height;
				w = img.width;
				if (h > w) {
					h = h > 200 ? 200 : h;
					w = w * h / img.height;
				} else {
					w = w > 200 ? 200 : w;
					h = h * w / img.width;
				}
				obj = {
					width: w + 'px',
					height: h + 'px'
				};
				this.style = obj;
			}
		},
	},
}




