
import Msg from 'vux/src/components/msg/index.vue'

export default {
data() {
return {
pageType: '',
showNoShopCode: false,
msg: {
title: '您没有店铺',
icon: 'info'
}
}
},
components: {
Msg,
},
created() {
this.pageType = this.$route.params.type;
if (this.pageType == 'undefined') {
this.showNoShopCode = true;
} else if (this.pageType == 'get') {
this.getShopCode();
}
},
methods: {
getShopCode() {
window.mywx.getUserInfo();
},
linkToJoin() {
var hash = 'joinus/sellers';
var url = location.href.split('#')[0] + '#/' + hash;
window.util.jump(url, {}, true);
}
},
}




