
import Swiper from 'vux/src/components/swiper/swiper.vue'

import imgBlock from '../components/ui/imgblock.vue'
import scroll from '../components/ui/scroll.list.vue'
import proCard from '../components/control/pro.card.vue'

import peopleLogo from '../assets/img/people-logo.png'

import gridImg1 from '../assets/img/new.png'
import gridImg2 from '../assets/img/hot.png'
import gridImg3 from '../assets/img/second.png'
import gridImg4 from '../assets/img/tuan.png'

import listIconNew from '../assets/img/list_icon_new.png'
import listIconHot from '../assets/img/list_icon_hot.png'
import listIconSecond from '../assets/img/list_icon_second.png'
import listIconTuan from '../assets/img/list_icon_tuan.png'

import labelNew from '../assets/img/label_new.png'
import labelHot from '../assets/img/label_hot.png'
import labelSecond from '../assets/img/label_second.png'
import labelTuan from '../assets/img/label_tuan.png'

export default {
data() {
return {
bannerList: [],
swiperScale: window.config.swiperScale,
farmMsg: [],
proList: [],
gridList: [],
farmProList: [],
shopMsg: [],

loading: false,
allLoad: false,
farmProPage: 1,
}
},
computed: {
farmHight: function() {
var w = window.innerWidth;
return w / 2

},
farmStyle: function() {
var w = window.innerWidth;
return {
height: w / 2 + 'px'
}
},
},
created() {
this.gridList = [{
img: gridImg1,
href: '/comlist/new',
name: '新品体验',
}, {
img: gridImg2,
href: '/comlist/hot',
name: '当季热销',
}, {
img: gridImg3,

href: '/comlist/combination',
name: '组合礼包',
}, {
img: gridImg4,

href: '/comlist/LargeCustomers',
name: '大客户专享',
}]

this.getBanner();
this.getShopinfo();
this.getFarmProList();
this.getFarmList();





},
activated() {
window.util.setTitle(this.shopMsg.title || '');
},
components: {
'f-img': imgBlock,
Swiper,
proCard,
scroll
},
methods: {
onItemClick() {
console.log('click item');
},
linkComList(one) {
window.util.jump(one.href);
},

getBanner() {
window.util.requestPost('bannerList', {}, (res) => {
if (res.code == 200) {
var da = res.data;
for (let i = 0; i < da.length; i++) {
if (/^(www)/.test(da[i].linkurl)) {
da[i].linkurl = 'http://' + da[i].linkurl;
} else if (da[i].linkurl && (!/^(www)/.test(da[i].linkurl) || /^(http)/.test(da[i].linkurl))) {
da[i].linkurl = location.href.split('#')[0] + window.Global.shopcode + '/' + da[i].linkurl.replace(/^\//, '');
}
var linkurl = da[i].linkurl;
da[i].url = linkurl ? linkurl.replace(/(^\s*|\s*$)/, '') : '';
da[i].img = da[i].picPath;
da[i].fallbackImg = '';
}
this.bannerList = da;
}
});
},
getShopinfo() {
var url = 'shopInfo';
window.util.requestPost(url, {}, (res) => {
if (res.code == 200) {
this.shopMsg = res.data;
window.util.setTitle(res.data.title);
}
});
},

getFarmList() {
var url = 'farmList';
window.util.requestPost(url, {}, (res) => {
if (res.code == 200) {
this.farmMsg = res.data;
}
})
},

linkToFarm(one) {
if (one.isSingle) {
var url = '/category/' + one.id;
} else {
var url = '/farm/' + one.id;
}
window.util.jump(url);
},

getProList(n) {
var url = 'listByType';
var param = {
saleType: n,
page: 1,
pageSize: 2
}
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
var da = res.data;
var labelList = [labelNew, labelHot, labelSecond, labelTuan]
for (let i = 0; i < da.length; i++) {
da[i].label = labelList[n - 1]
}
var obj = {};
switch (n) {
case 1:
obj.name = '新品体验';
obj.icon = listIconNew;
obj.href = '/comlist/new';
obj.list = da;
break;
case 2:
obj.name = '当季热销';
obj.icon = listIconHot;
obj.href = '/comlist/hot';
obj.list = da;
break;
case 3:
obj.name = '限时秒杀';
obj.icon = listIconSecond;
obj.href = '/second';
obj.list = da;
break;
case 4:
obj.name = '拼团福利';
obj.icon = listIconTuan;
obj.href = '/platoon';
obj.list = da;
break;
}
obj.type = n;
this.$set(this.proList, n - 1, obj);
}
})
},
loadMore() {
++this.farmProPage;
this.getFarmProList();
},
getFarmProList() {
var pageSize = 10;
var url = 'allProList';
var param = {
page: this.farmProPage,
pageSize: pageSize
};
this.loading = true;
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
var da = res.data;
this.loading = false;
da.forEach((item) => {
item.stock = Number(item.stock);
});
this.farmProList = this.farmProList.concat(da);
if (da.length < pageSize) {
this.allLoad = true;
}
}
})
},
}
}




