import pingtuanImg from '../assets/img/pingtuan.png'
import leftImg from '../assets/img/logo.png'
import actPeo from '../assets/img/people-logo.png'
import lazyPeo from '../assets/img/head_gray.png'

import imgBlock from '../components/ui/imgblock.vue'
export default {
	data() {
		return {
			proId: '',
			pingtuanImg,
			leftImg,
			actPeo,
			lazyPeo,
			attenderList: Array(0)
		}
	},
	components: {
		imgBlock,
	},
	created() {
		this.proId = this.$route.params.id;
		var atte = [];
		for (let i = 0; i < 10; i++) {
			if (i < 5) {
				atte.push(actPeo);
			} else {
				atte.push(lazyPeo);
			}
		}
		this.attenderList = atte;
	},
}




