import scrollList from '../ui/scroll.list.vue'
import imgBlock from '../ui/imgblock.vue'

import peopleLogo from '../../assets/img/people-logo.png'
export default {
	data() {
		return {
			rateList: [],
			peopleLogo,

			proId: '',
			pageSize: 10,
			page: 0,
			loading: false,
			allLoad: false,
		}
	},
	props: {
		id: {
			type: String,
		}
	},
	components: {
		scrollList,
		imgBlock,
	},
	created() {
		this.proId = this.$route.params.id;
		this.getProRateList();
		this.getBaseDetail();
	},
	methods: {
		loadMore() {
			this.getProRateList();
		},
		getProRateList() {
			if (this.loading) {
				return;
			}
			var url = "proRateList";
			var param = {
				goodsId: this.proId,
				page: ++this.page,
				pageSize: this.pageSize
			};
			this.loading = true;
			window.util.requestPost(url, param, (res) => {
				this.loading = false;
				if (res.code = 200) {
					var da = res.data;
					for (let i = 0; i < da.length; i++) {
						da[i].date = new Date(da[i].ratetime).Format('yyyy-mm-dd');
					}
					this.rateList = da;
					if (da.length < this.pageSize) {
						this.allLoad = true;
					}
				} else {
					console.log('proBanner get failt');
				}
			});
		},
		getBaseDetail() {
			var url = "proBaseDetail"
			var param = {
				goodsId: this.proId
			};
			window.util.requestPost(url, param, (res) => {
				if (res.code == 200) {
					this.setShareMsg(res.data);
				} else {
					console.log('baseData get failt');
				}
			});
		},
		setShareMsg(baseData) {
			var shareObj = {};
			shareObj.imgUrl = baseData.smallPic;
			shareObj.title = baseData.title;
			shareObj.link = location.href;
			shareObj.desc = $('title').text();
			window.mywx.share(shareObj);
		},
	}
}




