import touxian from '../../assets/img/logo.png'

import imgBlock from '../ui/imgblock.vue'
import progressBar from '../ui/progress.bar.vue'

import shareImg from '../../assets/img/share.png'
import smallCar from '../../assets/img/car_small.png'
export default {
	data() {
		return {
			touxian,
			shareImg,
			smallCar,
		}
	},
	components: {
		imgBlock,
		progressBar,
	},
	created() {

	},
	methods: {
		joinPlatoon() {
			var url = '/platoonsuccess/id';
			window.util.jump(url);
		},
	},
}




