
import Swiper from 'vux/src/components/swiper/swiper.vue'

import imgBlock from '../ui/imgblock.vue'
import video from '../ui/video.vue'

import peopleLogo from '../../assets/img/people-logo.png'

import noimg from '../../assets/img/small_noimg.png'

export default {
data() {
return {
proId: '',
peopleLogo,
noimg,
swiperScale: window.config.swiperScale,
showAppraisal: false,

baseData: {},
detailData: [],
bannerList: [],
rateList: [],

currentIndex: 0,
}
},
components: {
Swiper,
imgBlock,
'f-video': video,
},
computed: {
saleType() {
var saleType = this.baseData.saleType;
var type = '';
switch (saleType) {
case 1:
type = '新品体验';
break;
case 2:
type = '当季热销';
break;
case 3:
type = '限时秒杀';
break;
case 4:
type = '拼团福利';
break;
}
return type;
},
labelClass() {
return 'pro_type_' + this.baseData.saleType;
}
},
created() {
this.proId = this.$route.params.id;
this.getProBanner();
this.getBaseDetail();
this.getProRateList()
this.getProDetail();
},
methods: {
changePage() {
var l = location;
var hash = l.hash.split('/');
hash[hash.length - 1] = 'appraisal';
hash = hash.join('/');
util.jump(hash);
},
getBaseDetail() {
var url = "proBaseDetail"
var param = {
goodsId: this.proId
};
window.util.requestPost(url, param, (res) => {
if (res.code == 200) {
this.baseData = res.data;
this.setShareMsg();
} else {
console.log('baseData get failt');
}
});
},
getProDetail() {
var url = "proDetail";
var param = {
goodsId: this.proId
};
window.util.requestPost(url, param, (res) => {
if (res.code = 200) {
this.detailData = res.data;
} else {
console.log('detailData get failt');
}
});
},
getProBanner() {
var url = "proBanner";
var param = {
goodsId: this.proId
};
window.util.requestPost(url, param, (res) => {
if (res.code = 200) {
var da = res.data;
for (let i = 0; i < da.length; i++) {
da[i].url = da[i].linkurl;
da[i].img = da[i].picPath;
da[i].title = da[i].title;
}
this.bannerList = res.data;
} else {
console.log('proBanner get failt');
}
});
},
getProRateList() {
var url = "proRateList";
var param = {
goodsId: this.proId,
page: 1,
pageSize: 1
};
window.util.requestPost(url, param, (res) => {
if (res.code = 200) {
var da = res.data;
for (let i = 0; i < da.length; i++) {
da[i].date = new Date(da[i].ratetime).Format('yyyy - mm - dd');
}
this.rateList = res.data;
} else {
console.log('proBanner get failt');
}
});
},
setShareMsg() {
var shareObj = {};
shareObj.imgUrl = this.baseData.smallPic;
shareObj.title = this.baseData.title;
shareObj.link = location.href;
shareObj.desc = $('title').text();
window.mywx.share(shareObj);
},
linkToDynamic() {
var url = '/dynamic/pro/' + this.proId + '/page';
window.util.jump(url);
},
onIndexChange(index) {
this.currentIndex = index;
},
viewPic() {
var list = [];
list = this.bannerList.map((one) => {
return one.img;
});
var url = list[this.currentIndex];
wx.previewImage({
current: url, // 当前显示图片的http链接
urls: list 
});
},
}
}




