import home_lazy from '../../assets/img/nav_home_lazy.png'
import home_act from '../../assets/img/nav_home_act.png'
import cat_lazy from '../../assets/img/nav_car_lazy.png'
import cat_act from '../../assets/img/nav_car_act.png'
import me_lazy from '../../assets/img/nav_me_lazy.png'
import me_act from '../../assets/img/nav_me_act.png'
export default {
	data() {
		return {
			oldNavList: [],
		}
	},
	computed: {
		hash() {
			return this.$store.state.hash;
		},
		navList() {
			var hasActChange = false;
			var navList = [
				{ name: '首页', icon_lazy: home_lazy, icon_act: home_act, hash: '/home', act: false },
				{ name: '购物车', icon_lazy: cat_lazy, icon_act: cat_act, hash: '/car', act: false },
				{ name: '我的', icon_lazy: me_lazy, icon_act: me_act, hash: '/me', act: false }
			];
			for (let i = 0; i < navList.length; i++) {
				var r = new RegExp(navList[i].hash)
				var isItemAct = r.test(this.$store.state.hash);
				navList[i].act = isItemAct;
				if (isItemAct) {
					hasActChange = true;
				}
			}
			if (/category/.test(this.$store.state.hash)) {
				this.oldNavList = navList;
				return navList;
			}
			if (!hasActChange) {
				return this.oldNavList;
			} else {
				this.oldNavList = navList;
				return navList;
			}
		},
		isAlive() {
			var isAlive = false;
			var showList = ['home', 'car', 'me'];
			for (let i = 0; i < showList.length; i++) {
				var regStr = showList[i] + '\($\|\\/)';
				if (new RegExp(regStr).test(this.$store.state.hash)) {
					isAlive = true;
				}
			}
			if (/personalcenter/.test(this.$store.state.hash)) {
				isAlive = false;
			}
			if (/category/.test(this.$store.state.hash)) {
				// if (window.history.length == 1) {
				// 	isAlive = true;
				// } else {
				// 	isAlive = false;
				// }
				isAlive = true;
			}
			return isAlive;
		},
	},
	created() {
		this.$store.commit('sethash');
	},
	methods: {
		jumpPage(one) {
			if (!one.act) {
				window.util.jump(one.hash);
			}
		}
	},

}




