export default {
	props: {
		percentage: {
			type: Number,
			default: 0
		},
		bgcolor: {
			type: String,
		},
		borderColor: {
			type: String,
		},
		height: {
			type: String,
		}
	},
	computed: {
		borStyle() {
			var obj = {}
			if (this.height) {
				obj.height = this.height;
			} else {
				obj.height = '10px';
			}
			if (this.borderColor) {
				obj.borderColor = this.borderColor;
			}
			if (this.height) {
				obj.borderRadius = (this.height / 2) + 'px';
			}
			return obj;
		},
		mainStyle() {
			var obj = {}
			if (this.bgcolor) {
				obj.background = this.bgcolor;
			}
			if (this.percentage) {
				obj.width = this.percentage + '%';
			}
			return obj;
		},
	}
}




